import { Suspense } from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import { styled } from '@mui/material'
import Header from '../components/Header'
import Polling from '../components/essential/Polling'
import Popups from '../components/essential/Popups'
import Web3ReactManager from '../components/essential/Web3ReactManager'
import { ModalProvider } from 'context/ModalContext'
import Footer from 'components/Footer'
import Content from './Content'
import Market from '../pages/market'
import Detail from '../pages/market/Detail'
import { langData } from '../langs/lang'
import { createI18n, I18nProvider } from 'react-simple-i18n'
import NFT from './profile/NFT'
import Staking from './profile/Staking'
import Inviter from './profile/Inviter'
import History from './profile/History'
import Fledgling from './fledgling'
import Ranking from './Ranking'
import { routes } from 'constants/routes'

const AppWrapper = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'flex-start',
  overflowX: 'hidden',
  [theme.breakpoints.down('md')]: {
    flexDirection: 'column',
    minHeight: '100vh'
  }
}))

const ContentWrapper = styled('div')({
  width: '100%'
  // maxHeight: '100vh'
  // overflow: 'auto'
})

const BodyWrapper = styled('div')(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  width: '100%',
  minHeight: `calc(100vh - ${theme.height.header} - ${theme.height.subHeader} - ${theme.height.footer})`,
  flex: 1,
  // overflowY: 'auto',
  overflowX: 'hidden',
  position: 'relative',
  paddingTop: 60,
  [theme.breakpoints.down('md')]: {
    minHeight: `calc(100vh - ${theme.height.subHeader}  - ${theme.height.mobileHeader})`
    // paddingTop: 20
  }
}))

export default function App() {
  return (
    <Suspense fallback={null}>
      <I18nProvider i18n={createI18n(langData, { lang: 'en' })}>
        <ModalProvider>
          <AppWrapper id="app">
            <ContentWrapper>
              <Header />
              <BodyWrapper id="body">
                <Popups />
                <Polling />
                <Web3ReactManager>
                  <Switch>
                    <Route path="/bird/:model/:id" component={Detail} />
                    <Route path={routes.profile.nfts} component={NFT} />
                    <Route path={routes.profile.staking} component={Staking} />
                    <Route path={routes.profile.invite} component={Inviter} />
                    <Route path={routes.profile.invite + '/:inviter'} component={Inviter} />
                    <Route path={routes.profile.history} component={History} />
                    <Route path={routes.Ranking} component={Ranking} />
                    <Route path={routes.Fledgling + '/:inviter'} component={Fledgling} />
                    <Route path={routes.Fledgling} component={Fledgling} />
                    <Route path={routes.market} component={Market} />
                    <Route exact strict path="/test" component={Content} />
                    <Route exact path={routes.profile.index} render={() => <Redirect to={routes.profile.nfts} />} />
                    <Route exact path="/" render={() => <Redirect to={routes.Fledgling} />} />
                  </Switch>
                </Web3ReactManager>
              </BodyWrapper>
              <Footer />
            </ContentWrapper>
          </AppWrapper>
        </ModalProvider>
      </I18nProvider>
    </Suspense>
  )
}
