import { useMemo, useState } from 'react'
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import { useTheme, Box, styled, Typography, Tooltip, TooltipProps, tooltipClasses } from '@mui/material'
import { NetworkContextName } from '../../constants'
import useENSName from '../../hooks/useENSName'
import { useWalletModalToggle } from '../../state/application/hooks'
import { isTransactionRecent, useAllTransactions } from '../../state/transactions/hooks'
import { TransactionDetails } from '../../state/transactions/reducer'
import { shortenAddress } from '../../utils'
import WalletModal from 'components/Modal/WalletModal/index'
import Spinner from 'components/Spinner'
import useBreakpoint from 'hooks/useBreakpoint'
import Button from 'components/Button/Button'
import refreshWhite from 'assets/images/refresh_white.png'
import AccountDetails from '../Modal/WalletModal/AccountDetails'
import Modal from '../Modal/AccountDetailsModal'

const ActionButton = styled(Button)(({ theme }) => ({
  background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)',
  fontSize: '14px',
  marginBottom: 15,
  borderRadius: '20px',
  '&:hover': {
    opacity: 0.8
  },
  [theme.breakpoints.down('sm')]: {
    maxWidth: 320,
    width: '100%',
    marginBottom: 0
  }
}))

const btnStyle = {
  background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)',
  fontSize: 14
}

const WalletStatusTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: '#ffffff',
    color: 'rgba(0, 0, 0, 0.87)',
    maxWidth: 380,
    width: 380,
    fontSize: theme.typography.pxToRem(12),
    border: '1px solid #dadde9',
    padding: '0 20px'
  }
}))

// we want the latest one to come first, so return negative if a is after b
function newTransactionsFirst(a: TransactionDetails, b: TransactionDetails) {
  return b.addedTime - a.addedTime
}

function Web3StatusInner({ setWalletView }: { setWalletView: any }) {
  const { account, error } = useWeb3React()
  const { ENSName } = useENSName(account ?? undefined)
  const allTransactions = useAllTransactions()
  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions)
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst)
  }, [allTransactions])
  const pending = sortedRecentTransactions.filter(tx => !tx.receipt).map(tx => tx.hash)
  const confirmed = sortedRecentTransactions.filter(tx => tx.receipt).map(tx => tx.hash)
  const hasPendingTransactions = !!pending.length
  const toggleWalletModal = useWalletModalToggle()
  const theme = useTheme()
  const isDownSm = useBreakpoint()

  if (account) {
    return (
      <WalletStatusTooltip
        title={
          isDownSm ? (
            ''
          ) : (
            <Box padding={'24px 0'}>
              <AccountDetails
                toggleWalletModal={toggleWalletModal}
                pendingTransactions={pending}
                confirmedTransactions={confirmed}
                ENSName={ENSName ?? undefined}
                openOptions={() => toggleWalletModal()}
              />
            </Box>
          )
        }
      >
        <Box
          sx={{ cursor: 'pointer' }}
          onClick={() => {
            if (isDownSm) setWalletView(true)
          }}
        >
          <Box
            sx={{
              height: { xs: 24, sm: 36 },
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)',
              borderRadius: '18px'
            }}
          >
            <div />
            {hasPendingTransactions ? (
              <Box sx={{ display: 'flex', alignItems: 'center', mr: { xs: 10, sm: 17 }, ml: { xs: 10, sm: 20 } }}>
                <Spinner color={'white'} size={isDownSm ? '10px' : '16px'} />
                <Box component="span" sx={{ ml: 3 }}>
                  <Typography sx={{ fontSize: { xs: 9, sm: 14 }, ml: 8, color: theme.palette.text.primary }} noWrap>
                    {pending?.length} Pending
                  </Typography>
                </Box>
              </Box>
            ) : (
              <Box
                width={'auto'}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  padding: '0 10px'
                }}
              >
                <img src={refreshWhite} style={{ width: 14, marginRight: 5 }} />
                <Typography
                  sx={{
                    fontSize: { xs: 9, sm: 14 },
                    color: 'white'
                  }}
                >
                  {ENSName || shortenAddress(account)}
                </Typography>
              </Box>
            )}
          </Box>
        </Box>
      </WalletStatusTooltip>
    )
  } else if (error) {
    return (
      <ActionButton
        style={btnStyle}
        width={isDownSm ? '138px' : '140px'}
        height={isDownSm ? '28px' : '36px'}
        fontSize={isDownSm ? '12px' : '14px'}
        onClick={toggleWalletModal}
      >
        {error instanceof UnsupportedChainIdError ? 'Wrong Network' : 'Error'}
      </ActionButton>
    )
  } else {
    return (
      <ActionButton
        style={btnStyle}
        width={isDownSm ? '168px' : '160px'}
        height={isDownSm ? '28px' : '36px'}
        fontSize={isDownSm ? '12px' : '14px'}
        onClick={toggleWalletModal}
      >
        <img src={refreshWhite} style={{ width: 14, marginRight: 5 }} />
        Connect Wallet
      </ActionButton>
    )
  }
}

export default function Web3Status() {
  const contextNetwork = useWeb3React(NetworkContextName)
  const { account, active } = useWeb3React()
  const { ENSName } = useENSName(account ?? undefined)
  const allTransactions = useAllTransactions()
  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions)
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst)
  }, [allTransactions])
  const pending = sortedRecentTransactions.filter(tx => !tx.receipt).map(tx => tx.hash)
  const confirmed = sortedRecentTransactions.filter(tx => tx.receipt).map(tx => tx.hash)
  const [walletView, setWalletView] = useState(false)
  const toggleWalletModal = useWalletModalToggle()

  if (!contextNetwork.active && !active) {
    return null
  }

  return (
    <>
      <Web3StatusInner setWalletView={setWalletView} />
      <WalletModal />
      <Modal customIsOpen={walletView} customOnDismiss={() => setWalletView(false)} maxWidth="560px" closeIcon={true}>
        <Box width={'100%'} padding="32px" display="flex" flexDirection="column" alignItems="center" gap={20}>
          <Box display="grid" gap="10px" width="100%" justifyContent="center">
            <AccountDetails
              toggleWalletModal={() => {
                setWalletView(false)
                toggleWalletModal()
              }}
              pendingTransactions={pending}
              confirmedTransactions={confirmed}
              ENSName={ENSName ?? undefined}
              openOptions={() => {
                setWalletView(false)
                toggleWalletModal()
              }}
            />
          </Box>
        </Box>
      </Modal>
    </>
  )
}
