import { useEffect, useState } from 'react'
import { Stack, Typography } from '@mui/material'

function TimeItem({ tag, num }: { tag: string; num: number }) {
  return (
    <Stack alignItems={'center'} spacing={10}>
      <Typography
        borderRadius={'10px'}
        fontFamily={'Arial-Black!important'}
        lineHeight={'60px'}
        textAlign={'center'}
        sx={{
          lineHeight: { xs: '80px', sm: '60px' },
          width: { xs: 80, sm: 60 },
          height: { xs: 80, sm: 60 },
          fontSize: 32,
          backgroundColor: '#F1F0F2'
        }}
      >
        {num}
      </Typography>
      <Typography fontSize={'12px'} color={'#8A899E'}>
        {tag}
      </Typography>
    </Stack>
  )
}

const formatTime = (d: number, h: number, m: number, s: number) => {
  if (d) {
    return `${d}d : ${h}h : ${m}m : ${s}s`
  } else {
    return `${h}h : ${m}m : ${s}s`
  }
}

const formatTimeView = (d: number, h: number, m: number, s: number) => {
  return (
    <>
      <TimeItem tag={'h'} num={h + d * 24} />
      {/* <TimeItem tag={'h'} num={h} /> */}
      <TimeItem tag={'m'} num={m} />
      <TimeItem tag={'s'} num={s} />
    </>
  )
}

export const getDeltaTime = (time: number, to = Date.now()) => {
  const correctedTime = time
  const delta = /*14*24*60*60*1000 -*/ (correctedTime - to) / 1000

  return delta > 0 ? delta : 0
}

export const toDeltaTimer = (delta: number) => {
  const d = Math.floor(delta / (60 * 60 * 24)) < 0 ? 0 : Math.floor(delta / (60 * 60 * 24))
  const h = Math.floor((delta / (60 * 60)) % 24) < 0 ? 0 : Math.floor((delta / (60 * 60)) % 24)
  const m = Math.floor((delta / 60) % 60) < 0 ? 0 : Math.floor((delta / 60) % 60)
  const s = Math.floor(delta % 60) < 0 ? 0 : Math.floor(delta % 60)
  return formatTime(d, h, m, s)
}

export const toDeltaTimerView = (delta: number) => {
  const d = Math.floor(delta / (60 * 60 * 24)) < 0 ? 0 : Math.floor(delta / (60 * 60 * 24))
  const h = Math.floor((delta / (60 * 60)) % 24) < 0 ? 0 : Math.floor((delta / (60 * 60)) % 24)
  const m = Math.floor((delta / 60) % 60) < 0 ? 0 : Math.floor((delta / 60) % 60)
  const s = Math.floor(delta % 60) < 0 ? 0 : Math.floor(delta % 60)
  return formatTimeView(d, h, m, s)
}

export const Timer = ({ timer, onZero }: { timer: number; onZero?: () => void }) => {
  const [time, setTime] = useState(getDeltaTime(timer))

  useEffect(() => {
    const tm = setInterval(() => setTime(getDeltaTime(timer)), 1000)
    return () => clearInterval(tm)
  }, [timer])

  useEffect(() => {
    if (!time) {
      onZero && onZero()
    }
  }, [time, onZero])

  return <>{toDeltaTimer(time)}</>
}

export const FledglingTimer = ({ timer, onZero }: { timer: number; onZero?: () => void }) => {
  const [time, setTime] = useState(getDeltaTime(timer))

  useEffect(() => {
    const tm = setInterval(() => setTime(getDeltaTime(timer)), 1000)
    return () => clearInterval(tm)
  }, [timer])

  useEffect(() => {
    if (!time) {
      onZero && onZero()
    }
  }, [time, onZero])

  return <>{toDeltaTimerView(time)}</>
}
