import { Stack, styled } from '@mui/material'
import logo from '../../assets/images/sub-header.png'
import mobileBg from '../../assets/images/mobile_market_bg.png'
import Image from '../Image'
import useBreakpoint from 'hooks/useBreakpoint'

const Wrapper = styled(Stack)(({ theme }) => ({
  width: '100%',
  height: '80px',
  backgroundColor: '#936fd3',
  [theme.breakpoints.down('sm')]: {
    backgroundColor: '#4b663b'
  }
}))

function SubHeader() {
  const isSmDown = useBreakpoint('sm')
  return (
    <Wrapper>
      <Image style={{ height: '100%', objectFit: 'cover' }} src={isSmDown ? mobileBg : logo} />
    </Wrapper>
  )
}

export default SubHeader
