import Modal from '../index'
import { Button, Stack, Typography } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import useModal from '../../../hooks/useModal'
import { useCallback, useEffect, useState } from 'react'
import { useUser, useValidAddress } from '../../../hooks/useUser'
import MessageBox from '../TransactionModals/MessageBox'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import Input from '../../Input'
import { isAddress } from '../../../utils'
import { ExternalLink } from '../../../theme/components'
import { updateInviter } from '../../../state/application/actions'
import { useDispatch } from 'react-redux'
import { useLocation, useHistory } from 'react-router-dom'

export default function RegisterModal({ inviter }: { inviter?: string }) {
  const { t } = useI18n()
  const dispatch = useDispatch()
  const { push } = useHistory()
  const { showModal, hideModal } = useModal()
  const { register } = useUser()
  const [typed, setTyped] = useState('')
  const [error, setError] = useState(false)
  const [address, setAddress] = useState<undefined | string>()
  const validAddress = useValidAddress(address)
  const { pathname } = useLocation()

  useEffect(() => {
    if (!inviter) {
      try {
        const formatAddress = window.atob(typed)
        if (isAddress(formatAddress)) {
          setError(false)
          setAddress(formatAddress)
        } else {
          setAddress(undefined)
          setError(true)
        }
        setAddress(window.atob(typed))
      } catch (e) {
        setError(true)
      }
    }
  }, [inviter, typed])

  const registerCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    register(inviter ? inviter : address ?? '')
      .then(() => {
        hideModal()
        dispatch(updateInviter({ inviter: '' }))
        push(pathname)
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, register, inviter, address, hideModal])

  return (
    <Modal title={t('tip')} closeIcon>
      {!!inviter ? (
        <Typography margin={30} textAlign={'center'} fontWeight={'bold'} fontSize={18}>
          {t('notRegister')}
        </Typography>
      ) : (
        <Stack>
          <Input
            placeholder={t('enterCode')}
            style={{
              padding: '0 10px',
              borderRadius: 10,
              margin: 30,
              marginBottom: 0,
              width: '-webkit-fill-available'
            }}
            value={typed}
            onChange={e => setTyped(e.target.value)}
          />
        </Stack>
      )}
      {!inviter && (
        <ExternalLink href={'https://discord.com/invite/6JcgvUzdPj'}>
          <Typography
            sx={{
              marginLeft: 30,
              marginTop: 10,
              fontSize: 12,
              color: '#8A899E',
              ':hover': {
                color: '#0167BC'
              }
            }}
          >
            Go to Discord to get the invitation code
          </Typography>
        </ExternalLink>
      )}
      <Button
        disabled={(!inviter && !validAddress) || error}
        onClick={registerCallback}
        style={{ color: 'white' }}
        sx={{
          backgroundColor: '#0167BC',
          height: 50,
          borderRadius: '25px',
          width: '-webkit-fill-available',
          margin: '30px 30px 30px',
          ':hover': {
            backgroundColor: '#0167BC'
          },
          ':disabled': {
            opacity: 0.6
          }
        }}
      >
        {!inviter && !typed ? t('register') : (!inviter && !validAddress) || error ? t('unValidCode') : t('register')}
      </Button>
    </Modal>
  )
}
