import { Box, useTheme } from '@mui/material'
import Image from '../../Image'
import { BASE_IMAGE_URL } from '../../../constants'

export function ModalCover({ id }: { id: string }) {
  const theme = useTheme()

  return (
    <Box sx={{ width: { xs: '100%', sm: theme.width.smallCard }, height: { xs: '100%', sm: theme.width.smallCard } }}>
      <Image
        style={{
          borderRadius: '6px',
          width: '100%',
          height: '100%',
          overflow: 'hidden',
          backgroundColor: '#F1F0F2'
        }}
        src={`${BASE_IMAGE_URL}${id}.png`}
      />
    </Box>
  )
}
