import { useCallback, useMemo, useState, useEffect, useRef } from 'react'
import { Box, Grid, Stack, Typography } from '@mui/material'
import Profile from './index'
import { BaseNFT, getAccountNFT, getSingleNFT, NFT } from '../../utils/fetch/graph'
import useAsyncMemo from '../../hooks/useAsyncMemo'
import { useActiveWeb3React } from '../../hooks'
import { DefaultCard, NFTCard } from '../../components/Card'
import Button, { WhiteButton } from '../../components/Button/Button'
import { useI18n } from 'react-simple-i18n'
import MintModal from '../../components/Modal/Profile/MintModal'
import SelectModal from '../../components/Modal/Profile/SelectModal'
import AssetModal from '../../components/Modal/Profile/AssetModal'
import { LYNKNFT_ADDRESS, PageModel, USDT } from '../../constants'
import { useNFT } from '../../hooks/useNFT'
import TransactionSubmittedModal from '../../components/Modal/TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../../components/Modal/TransactionModals/MessageBox'
import useModal from '../../hooks/useModal'
import { Token, TokenAmount } from '../../constants/token'
import { tryParseAmount } from '../../utils/parseAmount'
import { ApprovalState, useApproveCallback } from '../../hooks/useApproveCallback'
import { useHistory } from 'react-router-dom'
import useBreakpoint from 'hooks/useBreakpoint'
import { useBlockNumber, useInviter } from '../../state/application/hooks'
import MakeOrderModal from '../../components/Modal/Order/MakeOrderModal'
import TransactionPendingModal from '../../components/Modal/TransactionModals/TransactionPendingModal'
import { useStaking } from '../../hooks/useStaking'
import { useValidAddress } from '../../hooks/useUser'
import TipModal from '../../components/Modal/base/TipModal'
import RegisterModal from '../../components/Modal/base/RegisterModal'
import JSBI from 'jsbi'
import config from 'config'
import moment from 'moment'

export default function MyNFT() {
  const { t } = useI18n()
  const { push } = useHistory()
  const isSmDown = useBreakpoint('sm')
  const { account, library } = useActiveWeb3React()
  const registered = useValidAddress(account ?? undefined)
  const inviter = useInviter()
  const validAddress = useValidAddress(inviter)
  const [name, setName] = useState('')
  const [index, setIndex] = useState(0)
  const [selectedNFT, setSelectedNFT] = useState<BaseNFT | undefined>(undefined)
  const [faction, setFaction] = useState(0)
  const [openMakeModal, setOpenMakeModal] = useState(false)
  const [sex, setSex] = useState(1)
  const [step, setStep] = useState<0 | 1 | 2 | 3>(0)
  const { showModal, hideModal } = useModal()
  const [token, setToken] = useState<Token | undefined>(USDT)
  const { mint } = useNFT()
  const blockNumber = useBlockNumber()
  const { withdraw } = useStaking()
  const [sellNFT, setSellNFT] = useState<NFT | undefined>()
  const [show, setShow] = useState(true)
  const timer = useRef<any>()
  const { allOpenTime } = config
  const [showMintCard, setshowMintCard] = useState(moment(new Date()).valueOf() > allOpenTime)

  useEffect(() => {
    timer.current = setInterval(() => {
      if (moment(new Date()).valueOf() > allOpenTime) {
        setshowMintCard(true)
        clearInterval(timer.current)
      } else {
        setshowMintCard(false)
      }
    }, 1000)
    return () => clearInterval(timer.current)
  }, [allOpenTime])

  const payCurrencyAmount = useMemo(() => {
    if (!token || !selectedNFT) return undefined
    return tryParseAmount(selectedNFT.price, token) as TokenAmount
  }, [selectedNFT, token])

  const [approvalState, approveCallback] = useApproveCallback(payCurrencyAmount, LYNKNFT_ADDRESS)

  const getNewIdList = useCallback(async () => {
    if (index === 0) return [undefined, undefined, undefined]
    let l1, l2, l3
    while (!l1 || !l2 || !l3) {
      if (!l1) {
        const randomL1 = Math.floor(Math.floor(1000 * Math.random()) + 1 + (faction * 2 + sex) * 10000) + 100000
        const resL1 = await getSingleNFT(randomL1.toString())
        if (!resL1) l1 = randomL1
      }
      if (!l2) {
        const randomL2 = Math.floor(Math.floor(1000 * Math.random()) + 1 + (faction * 2 + sex) * 10000) + 200000
        const resL2 = await getSingleNFT(randomL2.toString())
        if (!resL2) l2 = randomL2
      }
      if (!l3) {
        const randomL3 = Math.floor(Math.floor(1000 * Math.random()) + 1 + (faction * 2 + sex) * 10000) + 300000
        const resL3 = await getSingleNFT(randomL3.toString())
        if (!resL3) l3 = randomL3
      }
    }
    return [l1, l2, l3]
  }, [faction, index, sex])

  const newIdList = useAsyncMemo(getNewIdList, [undefined, undefined, undefined], [index])
  const newNFTList: (BaseNFT | undefined)[] = useMemo(() => {
    return newIdList?.map((id, index) => {
      return id
        ? {
            id: id.toString(),
            name,
            level: '0',
            grade: index === 0 ? 'Common' : index === 1 ? 'Special' : 'Rare',
            price: ['10', '100', '300'][index]
          }
        : undefined
    })
  }, [name, newIdList])

  const getMyNFT = useCallback(async () => {
    if (!account) return []
    return await getAccountNFT(account ?? '')
  }, [account])

  const NFTList: NFT[] = useAsyncMemo(getMyNFT, [], [blockNumber])

  const mintCallback = useCallback(() => {
    if (!library || !account || !selectedNFT?.id) return
    showModal(<TransactionPendingModal />)
    mint(selectedNFT.id, USDT.address, name)
      .then(() => {
        setName('')
        setStep(0)
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        setStep(0)
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [account, hideModal, library, mint, name, selectedNFT?.id, showModal])

  const withdrawCallback = useCallback(async () => {
    if (!selectedNFT) return
    showModal(<TransactionPendingModal />)
    withdraw(selectedNFT?.id)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [selectedNFT, showModal, withdraw, hideModal])

  return (
    <Box onTouchMove={() => setShow(!show)}>
      <MakeOrderModal
        estimatePrice={
          sellNFT?.charisma.toString() &&
          sellNFT?.vitality.toString() &&
          sellNFT?.intellect.toString() &&
          sellNFT?.dexterity.toString()
            ? Number(
                sellNFT?.charisma +
                  Number(sellNFT?.vitality) * 0.05 +
                  Number(sellNFT?.intellect) * 0.05 +
                  Number(sellNFT?.dexterity) * 0.05
              )
                .toFixed(4)
                .replace(/0+$/g, '')
            : '--'
        }
        isOpen={openMakeModal}
        onDismiss={() => setOpenMakeModal(false)}
        id={sellNFT?.id ?? ''}
        name={sellNFT?.name ?? '--'}
      />
      <MintModal
        isOpen={step === 1}
        name={name}
        onSetName={name => setName(name.replace(/[^\a-\z0-9]/g, '').substring(0, 15))}
        sex={sex}
        onSetSex={sex => setSex(sex)}
        faction={faction}
        onSetFaction={faction => setFaction(faction)}
        customOnDismiss={() => setStep(0)}
        onNext={() => {
          setIndex(index + 1)
          setStep(2)
        }}
      />

      <SelectModal
        selectedNFT={selectedNFT}
        list={newNFTList}
        isOpen={step === 2}
        customOnDismiss={() => setStep(0)}
        onSelect={nft => setSelectedNFT(nft)}
        onNext={() => {
          setStep(3)
        }}
        onRefresh={() => setIndex(index + 1)}
        onBack={() => {
          setStep(1)
        }}
      />

      <AssetModal
        pending={approvalState === ApprovalState.PENDING}
        status={approvalState === ApprovalState.NOT_APPROVED ? t('approve') : t('confirm')}
        payAmount={payCurrencyAmount}
        onSelectAsset={token => setToken(token)}
        customOnDismiss={() => setStep(0)}
        isOpen={step === 3}
        onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : mintCallback}
        onBack={() => setStep(2)}
      />

      <Profile>
        <Grid container mt={10} mb={20} spacing={isSmDown ? 15 : 20}>
          {showMintCard ? (
            <DefaultCard marketMobile={isSmDown} small={isSmDown}>
              <Stack height={isSmDown ? 120 : 134} width={'100%'} padding={'25px 10px'} spacing={15}>
                <Button
                  onClick={() => {
                    if (!registered) {
                      if (!inviter) {
                        showModal(<RegisterModal />)
                      } else if (validAddress) {
                        showModal(<RegisterModal inviter={inviter} />)
                      } else {
                        showModal(<TipModal tip={t('unValidLink')} />)
                      }
                      return
                    }
                    setName('')
                    setSex(1)
                    setFaction(0)
                    setSelectedNFT(undefined)
                    setStep(1)
                  }}
                  width={'100%'}
                >
                  {t('mint')}
                </Button>
                <Button
                  onClick={() =>
                    push({
                      pathname: '/market',
                      search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                    })
                  }
                  width={'100%'}
                >
                  {t('buy')}
                </Button>
              </Stack>
            </DefaultCard>
          ) : null}
          {NFTList.map(nft => {
            return (
              <NFTCard
                showTip={show}
                key={nft.id}
                item={nft}
                onClick={() =>
                  push({
                    pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                    search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                  })
                }
                marketMobile={isSmDown}
                small={isSmDown}
              >
                <Box height={isSmDown ? 90 : 108}>
                  {/*<Stack padding={15} direction={'row'} spacing={6} alignItems={'center'}>*/}
                  {/*  <Typography fontWeight={'bold'}>*/}
                  {/*    {t('prePrice')}:{' '}*/}
                  {/*    {nft?.charisma.toString() &&*/}
                  {/*    nft?.vitality.toString() &&*/}
                  {/*    nft?.intellect.toString() &&*/}
                  {/*    nft?.dexterity.toString()*/}
                  {/*      ? Number(*/}
                  {/*          Number(*/}
                  {/*            nft?.charisma +*/}
                  {/*              Number(nft?.vitality) * 0.05 +*/}
                  {/*              Number(nft?.intellect) * 0.05 +*/}
                  {/*              Number(nft?.dexterity) * 0.05*/}
                  {/*          )*/}
                  {/*            .toFixed(4)*/}
                  {/*            .replace(/0+$/g, '')*/}
                  {/*        )*/}
                  {/*      : '--'}*/}
                  {/*  </Typography>*/}
                  {/*  <Image height={'16px'} width={'16px'} src={USDT_IMG} />*/}
                  {/*</Stack>*/}
                  <Stack padding={'2px 10px'} spacing={10} direction={'row'} height={'100%'} alignItems={'center'}>
                    {/*<Button onClick={() => push(`/bird/${PageModel.DEFAULT}/${nft.id}`)} width={'100%'}>*/}
                    {/*  {t('detail')}*/}
                    {/*</Button>*/}
                    {nft.isList && (
                      <Button
                        onClick={() =>
                          push({
                            pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                            search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                          })
                        }
                        width={'100%'}
                      >
                        {t('cancel')}
                      </Button>
                    )}

                    {nft.isStaking && (
                      <Box
                        width={'100%'}
                        mt={10}
                        gap={isSmDown ? 5 : 20}
                        display="grid"
                        gridTemplateColumns={'1fr 1fr'}
                      >
                        <WhiteButton onClick={withdrawCallback} width={'100%'}>
                          {t('withdraw')}
                        </WhiteButton>
                        <Box sx={{ ' button': { xs: { fontSize: 12 } } }}>
                          <Button width={'100%'}>{t('claim')}</Button>
                        </Box>
                      </Box>
                    )}

                    {!nft.isStaking && !nft.isList && (
                      <>
                        {isSmDown ? (
                          <>
                            <Stack
                              height={40}
                              width={'100%'}
                              direction={'row'}
                              sx={{
                                border: '1px solid #F1F0F2',
                                borderRadius: 20,
                                ' p': {
                                  flex: 1,
                                  cursor: 'pointer',
                                  lineHeight: '40px',
                                  textAlign: 'center'
                                }
                              }}
                            >
                              <Typography
                                onClick={e => {
                                  e.stopPropagation()
                                  if (showMintCard) {
                                    if (!nft.level || JSBI.lessThan(JSBI.BigInt(nft.level), JSBI.BigInt(2))) {
                                      showModal(<TipModal tip={t('mustHigherThanLevel2')} />)
                                      return
                                    }
                                    setSellNFT(nft)
                                    setOpenMakeModal(true)
                                  }
                                }}
                                borderRight={'1px solid #F1F0F2'}
                                sx={{
                                  cursor: showMintCard ? 'default' : 'not-allowed',
                                  opacity: showMintCard ? 1 : 0.5
                                }}
                              >
                                {t('sale')}
                              </Typography>
                              <Typography
                                onClick={e => {
                                  e.stopPropagation()
                                  if (showMintCard) {
                                    push({
                                      pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                                      search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                                    })
                                  }
                                }}
                                borderRight={'1px solid #F1F0F2'}
                                sx={{
                                  cursor: showMintCard ? 'default' : 'not-allowed',
                                  opacity: showMintCard ? 1 : 0.5
                                }}
                              >
                                {t('deposit')}
                              </Typography>
                              <Typography
                                onClick={() =>
                                  push({
                                    pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                                    search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                                  })
                                }
                                color={'#0167BC'}
                              >
                                {t('detail')}
                              </Typography>
                            </Stack>
                          </>
                        ) : (
                          <>
                            <WhiteButton
                              onClick={e => {
                                e.stopPropagation()
                                if (!nft.level || JSBI.lessThan(JSBI.BigInt(nft.level), JSBI.BigInt(2))) {
                                  showModal(<TipModal tip={t('mustHigherThanLevel2')} />)
                                  return
                                }
                                setSellNFT(nft)
                                setOpenMakeModal(true)
                              }}
                              width={'100%'}
                              disabled={!showMintCard}
                            >
                              {t('sale')}
                            </WhiteButton>
                            <WhiteButton
                              onClick={() =>
                                push({
                                  pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                                  search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                                })
                              }
                              width={'100%'}
                              disabled={!showMintCard}
                            >
                              {t('deposit')}
                            </WhiteButton>
                            <Button
                              onClick={() =>
                                push({
                                  pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
                                  search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
                                })
                              }
                              width={'100%'}
                            >
                              {t('detail')}
                            </Button>
                          </>
                        )}
                      </>
                    )}
                  </Stack>
                </Box>
              </NFTCard>
            )
          })}
        </Grid>
      </Profile>
    </Box>
  )
}
