import { useCallback, useEffect, useState, useRef } from 'react'
import { Box, Stack, styled, Typography, useTheme } from '@mui/material'
import Image from '../../components/Image'
import header_bg from '../../assets/images/fledgling_header_bg.png'
import header_bg_mobile from '../../assets/images/fledgling_header_bg_mobile.png'
import fire from '../../assets/images/fire.png'
import invite from '../../assets/images/invite.png'
import build from '../../assets/images/build.png'
import Divider from '../../components/Divider'
import Button, { BlueButton } from '../../components/Button/Button'
import ActionButton from '../../components/Button/ActionButton'
import MintModal from '../../components/Modal/fiedgling/MintModal'
import { useFledgling, useIsWhitelistInFledgling } from '../../hooks/useFledgling'
import { useHistory, useParams, useLocation } from 'react-router-dom'
import { tryParseAmount } from '../../utils/parseAmount'
import { LYNKNFT_ADDRESS, USDT } from '../../constants'
import { useTokenBalance } from '../../state/wallet/hooks'
import { useActiveWeb3React } from '../../hooks'
import { ApprovalState, useApproveCallback } from '../../hooks/useApproveCallback'
import { useI18n } from 'react-simple-i18n'
import TransactionPendingModal from '../../components/Modal/TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../../components/Modal/TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../../components/Modal/TransactionModals/MessageBox'
import useModal from '../../hooks/useModal'
import { useUser, useValidAddress } from '../../hooks/useUser'
import TipModal from '../../components/Modal/base/TipModal'
import InviteModal from '../../components/Modal/base/InviteModal'
import RegisterModal from '../../components/Modal/base/RegisterModal'
import { useUserHasSubmitted } from '../../state/transactions/hooks'
import OutlineButton from '../../components/Button/OutlineButton'
import { Dots } from '../../theme/components'
import useBreakpoint from '../../hooks/useBreakpoint'
import { useDispatch } from 'react-redux'
import { updateInviter } from '../../state/application/actions'
import { useWalletModalToggle } from '../../state/application/hooks'
import { FledglingTimer } from '../../components/Timer'
import Spinner from '../../components/Spinner'
import moment from 'moment'
import { useInviter } from '../../state/application/hooks'
import config from 'config'

const StyledWrapper = styled(Box)({
  width: '100%',
  margin: 'auto',
  display: 'flex'
})

function DataItem({ tag, num }: { tag: string; num: string }) {
  return (
    <Stack alignItems={'center'} spacing={10}>
      <Typography borderRadius={'10px'} fontFamily={'Arial-Black!important'} textAlign={'center'} sx={{ fontSize: 32 }}>
        {num}
      </Typography>
      <Typography fontSize={'12px'} color={'#8A899E'}>
        {tag}
      </Typography>
    </Stack>
  )
}

const { mintStartTime, mintEndTime } = config

export default function Fledgling() {
  const { account } = useActiveWeb3React()
  const theme = useTheme()
  const { t } = useI18n()
  const dispatch = useDispatch()
  const params = useParams<{ inviter: string }>()
  const { push } = useHistory()
  const timer = useRef<any>()
  // const inviter = params.inviter ? window.atob(params.inviter) : undefined
  const inviter = useInviter()
  const [isMintModalOpen, setIsMintModalOpen] = useState(false)
  const { remainNum, totalNum, mint } = useFledgling()
  const payAmount = tryParseAmount('5000', USDT)
  const toggleWalletModal = useWalletModalToggle()
  const balance = useTokenBalance(account ?? undefined, USDT)
  const [approvalState, approveCallback] = useApproveCallback(payAmount, LYNKNFT_ADDRESS)
  const enoughAsset = balance && payAmount && !balance.lessThan(payAmount)
  const { showModal, hideModal } = useModal()
  const { loading, isWhitelist } = useIsWhitelistInFledgling(account ?? undefined)
  const validAddress = useValidAddress(inviter)
  const mintBle = useValidAddress(account ?? undefined)
  const { earlyBirdMinted, earlyBirdId } = useUser()
  const { submitted: registering } = useUserHasSubmitted(`${account}_register` ?? undefined)
  const { submitted: minting } = useUserHasSubmitted(`${account}_fledgling_mint` ?? undefined)
  const isSmDown = useBreakpoint('sm')
  const [imgBoxHeight, setImgBoxHeight] = useState<any>(
    isSmDown ? (window.innerWidth / 1500) * 560 : (window.innerWidth / 1920) * 400
  )
  const [mintTime, setMintTime] = useState(moment(new Date()).valueOf() < mintStartTime ? mintStartTime : mintEndTime)
  const [mintTimeEnd, setMintTimeEnd] = useState(moment(new Date()).valueOf() > mintEndTime)
  const { search } = useLocation()

  useEffect(() => {
    timer.current = setInterval(() => {
      if (moment(new Date()).valueOf() > mintEndTime) {
        setMintTimeEnd(true)
        clearInterval(timer.current)
      } else if (moment(new Date()).valueOf() > mintStartTime && moment(new Date()).valueOf() < mintEndTime) {
        setMintTimeEnd(false)
        setMintTime(mintEndTime)
      } else {
        setMintTimeEnd(true)
      }
    }, 1000)
    return () => clearInterval(timer.current)
  }, [])

  useEffect(() => {
    if (params.inviter || search) {
      try {
        const inviterAddress = params.inviter ? window.atob(params.inviter) : window.atob(search.split('?referrer=')[1])
        dispatch(updateInviter({ inviter: inviterAddress }))
      } catch (e) {
        console.log('')
      }
    }
  }, [dispatch, params, search])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const handleResize = () => {
    setImgBoxHeight('auto')
    window.removeEventListener('resize', handleResize)
  }
  useEffect(() => {
    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const mintCallback = useCallback(async () => {
    if (earlyBirdMinted) {
      push('/profile/nfts')
      return
    }

    showModal(<TransactionPendingModal title="Confirm" />)
    mint()
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal title="Confirm" />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error" title="Fail">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [earlyBirdMinted, showModal, mint, push, hideModal])

  return (
    <Box sx={{ backgroundColor: theme.palette.primary.main, width: '100%' }}>
      <MintModal
        approved={approvalState === ApprovalState.APPROVED}
        submitted={minting}
        finish={!!earlyBirdMinted}
        nextId={earlyBirdId ? earlyBirdId.toString() : undefined}
        isOpen={isMintModalOpen}
        customOnDismiss={() => setIsMintModalOpen(false)}
      >
        <Box>
          <ActionButton
            pending={minting || approvalState === ApprovalState.PENDING}
            pendingText={minting ? t('minting') : t('approving')}
            disableAction={!enoughAsset && !earlyBirdMinted}
            style={{ background: 'black', width: '100%', height: 50, fontSize: 16 }}
            onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : mintCallback}
            actionText={
              earlyBirdMinted
                ? 'Finish'
                : !enoughAsset
                ? t('insufficient')
                : approvalState === ApprovalState.NOT_APPROVED
                ? t('approve')
                : t('mint')
            }
          />
        </Box>
      </MintModal>

      <StyledWrapper>
        <Box sx={{ width: '100%' }}>
          <Image style={{ width: '100%', height: imgBoxHeight }} src={isSmDown ? header_bg_mobile : header_bg} />
        </Box>
      </StyledWrapper>

      <Box sx={{ backgroundColor: 'white', padding: '50px 20px 120px 20px' }} margin={'auto'} width={'100%'}>
        <StyledWrapper
          sx={{
            maxWidth: 1200,
            flexWrap: { xs: 'wrap', sm: 'unset' },
            justifyContent: { xs: 'center', sm: 'unset' },
            flexDirection: { xs: 'column-reverse', sm: 'unset' }
          }}
        >
          <Stack
            sx={{
              maxWidth: { sm: '55%', xs: 'unset' },
              mt: { xs: 20, sm: 0 },
              padding: { xs: 0, sm: '16px 40px 16px 16px', md: '16px 100px 16px 16px' }
            }}
          >
            <Typography fontFamily={'Arial-Black!important'} sx={{ fontSize: 32, textAlign: { xs: 'center' } }}>
              The Finder NFT
            </Typography>
            <Typography mt={20}>
              The Finders come from the Nexus, a central point of all spacetime coordinates. These beings have existed
              from the start of the known omniverse and have observed the birth of countless worlds. They collect all
              the knowledge and data from all Metaverses, preserving it by synchronizing their minds in cohesion. From
              time to time, an extraordinary Metaverse with the potential to advance beyond the limits of other
              Metaverses appear, prompting the Finders to act. Stepping into LYNKVERSE through the rift, the Finders set
              out to carry out their mission - to select a chosen few to guide other inhabitants and ensure the
              Metaverse grows into its full potential.
            </Typography>
            <Typography mt={20}>
              Every Finder creates soulseeds containing a portion of its powers and bestow them upon the chosen ones. By
              assimilating with the soulseed, a new race is born. Come carry the mantle of hope and become the leaders
              of the new age…
            </Typography>
          </Stack>

          <Box
            sx={{
              marginTop: { xs: 0, sm: 20 },
              pl: { xs: 0, sm: 20, md: 40, lg: 50 },
              borderLeft: { sm: '1px solid #F1F0F2', xs: 'unset' }
            }}
          >
            <Stack>
              <Stack direction={'row'} sx={{ justifyContent: 'space-between' }}>
                <Typography fontWeight={'bold'} display={'flex'} alignItems={'center'}>
                  <Image
                    src={fire}
                    style={{ width: 16, height: 'fit-content', objectFit: 'contain', marginRight: 10 }}
                  />
                  {t('countdown')}
                </Typography>
                <Stack direction={'row'} spacing={20} alignSelf={'center'}>
                  <Button
                    onClick={() => {
                      if (!account) {
                        toggleWalletModal()
                        return
                      }
                      if (!mintBle) {
                        if (!inviter) {
                          showModal(<RegisterModal />)
                        } else if (validAddress) {
                          showModal(<RegisterModal inviter={inviter} />)
                        } else {
                          showModal(<TipModal tip={t('unValidLink')} />)
                        }
                      } else {
                        showModal(<InviteModal router={'fledgling'} />)
                      }
                    }}
                    style={{ backgroundColor: '#ffffff', border: '1px #0167BC solid', color: '#0167BC' }}
                  >
                    <Image style={{ marginRight: 10 }} width={10} src={invite} />
                    {t('inviteFriend')}
                  </Button>
                </Stack>
              </Stack>
              <Stack
                width={'100%'}
                display={'flex'}
                direction={'row'}
                justifyContent={'space-between'}
                padding="25px 0"
              >
                <FledglingTimer timer={mintTime} />
              </Stack>

              <Divider />
              <Stack direction={'row'} margin={'auto'} spacing={100} padding="25px">
                <DataItem tag={t('earlyBirdTotal')} num={totalNum ? totalNum.toString() : '--'} />
                <DataItem tag={t('earlyBirdLeft')} num={totalNum ? remainNum.toString() : '--'} />
              </Stack>
              <Box sx={{ height: { xs: 50, sm: 50 } }}>
                {minting ? (
                  <OutlineButton disabled height={50} color={'#0167BC'}>
                    {t('minting')}
                    <Dots />
                  </OutlineButton>
                ) : (
                  <BlueButton
                    disabled={earlyBirdMinted || loading || registering || mintTimeEnd}
                    style={{
                      width: '100%',
                      height: '100%',
                      fontSize: 16,
                      background:
                        earlyBirdMinted ||
                        mintTime === mintStartTime ||
                        ((mintTimeEnd || remainNum?.toString() === '0') && totalNum)
                          ? '#00000052'
                          : 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)'
                    }}
                    onClick={() => {
                      if (!account) {
                        toggleWalletModal()
                        return
                      }
                      if (!isWhitelist && !validAddress && !mintBle) {
                        if (!inviter) {
                          showModal(<RegisterModal />)
                        } else {
                          showModal(<TipModal tip={t('unValidLink')} />)
                        }
                        return
                      }
                      if (!mintBle && inviter) {
                        showModal(<RegisterModal inviter={inviter} />)
                        return
                      }
                      setIsMintModalOpen(true)
                    }}
                  >
                    {registering ? (
                      <>
                        <Spinner marginRight={16} />
                        Registering
                      </>
                    ) : (
                      <>
                        <img src={build} style={{ width: 20, marginRight: 5 }} />
                        {earlyBirdMinted
                          ? t('minted')
                          : mintTime === mintStartTime
                          ? t('mint')
                          : (mintTimeEnd || remainNum?.toString() === '0') && totalNum
                          ? t('soldOut')
                          : t('mint')}
                      </>
                    )}
                  </BlueButton>
                )}
              </Box>
            </Stack>
          </Box>
        </StyledWrapper>
      </Box>
    </Box>
  )
}
