import React from 'react'
import { Box, Typography } from '@mui/material'
import Modal from 'components/Modal'
// import { ReactComponent as SuccessIcon } from 'assets/componentsIcon/statusIcon/success_icon.svg'
// import { ReactComponent as FailureIcon } from 'assets/componentsIcon/statusIcon/failure_icon.svg'
import { ReactComponent as SupportIcon } from 'assets/componentsIcon/statusIcon/support_icon.svg'
// import { ReactComponent as Error } from 'assets/componentsIcon/statusIcon/error_icon.svg'
import { ReactComponent as Warning } from 'assets/componentsIcon/statusIcon/warning_icon.svg'
import SuccessIcon from 'assets/images/success_icon.png'
import FailureIcon from 'assets/images/fail_icon.png'
import Button from 'components/Button/Button'
import useModal from 'hooks/useModal'
import useBreakpoint from 'hooks/useBreakpoint'

interface Props {
  type: 'success' | 'failure' | 'support' | 'error' | 'warning'
  children?: React.ReactNode
  title?: string
  width?: string
  header?: string
  action?: () => void
  actionText?: string
}

export default function MessageBox({ type, title, children, width = '480px', header, action, actionText }: Props) {
  const { hideModal } = useModal()
  const isSmDown = useBreakpoint('sm')

  const icon =
    type === 'success' ? (
      <img height={isSmDown ? 40 : 80} width={isSmDown ? 40 : 80} src={SuccessIcon} />
    ) : type === 'failure' ? (
      <img height={isSmDown ? 40 : 80} width={isSmDown ? 40 : 80} src={FailureIcon} />
    ) : type === 'support' ? (
      <SupportIcon />
    ) : type === 'warning' ? (
      <Warning />
    ) : (
      <img height={isSmDown ? 40 : 80} width={isSmDown ? 40 : 80} src={FailureIcon} />
    )

  return (
    <Modal width={width} title={title} closeIcon>
      <Box display={'grid'} alignItems={'center'} padding={'30px'} justifyItems="center" gap="20px">
        <Box>{icon}</Box>
        {header && (
          <Typography variant="h6" fontSize={16}>
            {header}
          </Typography>
        )}
        <Box fontSize="18px" textAlign="center" display="grid" justifyItems="center" width="100%">
          {children}
        </Box>

        <Box display="flex" justifyContent="space-around" width="100%">
          <Button
            style={{
              borderRadius: 25
            }}
            width="140px"
            height="50px"
            onClick={hideModal}
          >
            Close
          </Button>
          {type === 'failure' && actionText && <Button onClick={action}>{actionText}</Button>}
        </Box>
      </Box>
    </Modal>
  )
}
