import Modal from '../index'
import { useI18n } from 'react-simple-i18n'
import { Grid, Skeleton, Stack, Typography, useTheme } from '@mui/material'
import { BaseNFT } from '../../../utils/fetch/graph'
import { NFTCard, SkeletonNFTCard } from '../../Card'
import ActionButton from '../../Button/ActionButton'
import Image from '../../Image'
import refresh from '../../../assets/images/refresh.png'
import { useState } from 'react'

export default function SelectModal({
  selectedNFT,
  list,
  isOpen,
  onSelect,
  onRefresh,
  onNext,
  onBack,
  customOnDismiss
}: {
  selectedNFT: BaseNFT | undefined
  list?: (BaseNFT | undefined)[]
  isOpen: boolean
  onSelect: (nft: BaseNFT) => void
  onRefresh: () => void
  onNext: () => void
  onBack?: () => void
  customOnDismiss: () => void
}) {
  const { t } = useI18n()
  const theme = useTheme()
  const [show, setShow] = useState(true)

  return (
    <Modal
      width={'fit-content'}
      maxWidth={'fit-content'}
      customIsOpen={isOpen}
      closeIcon
      backIcon
      onBack={onBack}
      customOnDismiss={customOnDismiss}
      title={t('profile.selectNFT')}
    >
      <div
        onTouchMove={e => {
          e.preventDefault()
          setShow(!show)
        }}
      >
        <Grid container sx={{ padding: '30px 24px', flexWrap: 'nowrap', overflowX: 'scroll' }} spacing={20}>
          {list?.map(nft => {
            return nft ? (
              <NFTCard
                showTip={show}
                showGrade
                selected={selectedNFT?.id === nft?.id}
                small
                key={nft.id}
                item={nft}
                onClick={() => onSelect(nft)}
              >
                <Typography margin={'10px'} color={theme.textColor.text5}>
                  {nft.price} U
                </Typography>
              </NFTCard>
            ) : (
              <SkeletonNFTCard small>
                <Skeleton sx={{ margin: '0 10px' }} />
              </SkeletonNFTCard>
            )
          })}
        </Grid>
      </div>
      <Stack mb={26} width={'100%'} alignItems={'center'} direction={'row-reverse'} sx={{ marginTop: { xs: 30 } }}>
        <ActionButton
          style={{ height: 50, width: '50%', background: 'black', margin: 'auto' }}
          disableAction={!selectedNFT}
          onAction={onNext}
          actionText={`${t('mint')} NFT`}
        />
        <Stack
          sx={{
            position: { xs: 'relative', sm: 'absolute' },
            height: 30,
            marginLeft: { xs: 30 },
            right: { sm: 50 },
            cursor: 'pointer'
          }}
          direction={'row'}
          spacing={10}
          alignItems={'center'}
        >
          <Image style={{ width: 16 }} src={refresh} />
          <Typography color={theme.textColor.primary} onClick={onRefresh}>
            {t('profile.reSelect')}
          </Typography>
        </Stack>
      </Stack>
    </Modal>
  )
}
