import Modal from '../index'
import { Typography } from '@mui/material'
import { useI18n } from 'react-simple-i18n'

export default function TipModal({ tip }: { tip: string }) {
  const { t } = useI18n()
  return (
    <Modal title={t('tip')} closeIcon>
      <Typography margin={80} textAlign={'center'} fontWeight={'bold'} fontSize={18}>
        {tip}
      </Typography>
    </Modal>
  )
}
