import { Checkbox, FormControlLabel } from '@mui/material'
import { ReactComponent as CheckboxIcon } from '../../assets/componentsIcon/checkbox.svg'
import { ReactComponent as CheckboxCheckedIcon } from '../../assets/componentsIcon/checkbox_checked.svg'
import * as React from 'react'

interface Props {
  checked: boolean
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void
  label?: string
  disabled?: boolean
}

export default function _Checkbox(props: Props) {
  const { checked, onChange, label = '', disabled } = props
  return (
    <FormControlLabel
      sx={{
        height: 50,
        margin: 0,
        borderRadius: '10px',
        padding: '0 10px',
        fontSize: 16,
        fontWeight: 400,
        justifyContent: 'space-between',
        // backgroundColor: '#fafafa',
        '&:hover': {
          backgroundColor: '#fafafa'
        }
      }}
      control={
        <Checkbox
          onChange={onChange}
          sx={{ padding: 0 }}
          icon={<CheckboxIcon />}
          checkedIcon={<CheckboxCheckedIcon width={18} height={18} />}
        />
      }
      label={label}
      labelPlacement="start"
      checked={checked}
      disabled={disabled}
    />
  )
}
