import { Box, Typography } from '@mui/material'
import Tooltip from '../../components/essential/QuestionHelper/Tooltip'
import { useState } from 'react'

export function TitleHelper({ text }: { text: string }) {
  const [show, setShow] = useState<boolean>(false)

  return (
    <Box style={{ width: '40%' }}>
      <Tooltip text={text} show={show}>
        <Typography
          onClick={() => {
            setShow(true)
          }}
          onMouseEnter={() => setShow(true)}
          onMouseLeave={() => {
            setShow(false)
          }}
          style={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}
          maxWidth={'100%'}
          sx={{ width: { xs: '40vw', md: '100%' } }}
        >
          {text}
        </Typography>
      </Tooltip>
    </Box>
  )
}
