import Modal from '../index'
import { Button, Typography, Box, styled } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import { useActiveWeb3React } from '../../../hooks'
import useCopyClipboard from '../../../hooks/useCopyClipboard'
import useMediaQuery from '@mui/material/useMediaQuery'

export default function InviteModal({ router }: { router: string }) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const [isCopied, setCopied] = useCopyClipboard()
  const [isCopiedCode, setCopiedCode] = useCopyClipboard()
  const matches = useMediaQuery('(min-width:600px)')

  const CopeBox = styled(Box)`
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    gap: 10px;
    margin: 20px 0;
    padding: 0 10px;
  `

  return (
    <Modal title={t('myInviteLink')} padding={'0'} closeIcon>
      <CopeBox>
        <Box
          sx={{
            fontSize: matches ? 16 : 14,
            whiteSpace: 'nowrap',
            fontWeight: 'bold',
            color: '#8A899E',
            width: matches ? 220 : 180
          }}
        >
          {`invitation link:`}
        </Box>
        <Typography
          onClick={() => setCopied(`${window.location.hostname}/#/${router}?referrer=${window.btoa(account ?? '')}`)}
          sx={{
            borderRadius: '25px',
            border: '1px #C5C4D6 solid',
            padding: matches ? '15px 5px' : '10px 5px',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            fontSize: matches ? 16 : 14,
            whiteSpace: 'nowrap',
            fontWeight: 'bold',
            width: 'calc(100%)',
            color: '#8A899E'
          }}
        >
          {`${window.location.hostname}/#/${router}/${window.btoa(account ?? '')}`}
        </Typography>
        <Button
          onClick={() => setCopied(`${window.location.hostname}/#/${router}?referrer=${window.btoa(account ?? '')}`)}
          sx={{
            color: 'white',
            backgroundColor: '#0167BC',
            height: matches ? 50 : 36,
            width: '80px',
            borderRadius: '25px',
            fontSize: matches ? 16 : 14,
            padding: '0 30px',
            ':hover': {
              backgroundColor: '#0167BC'
            }
          }}
        >
          {isCopied ? t('copied') : t('copy')}
        </Button>
      </CopeBox>
      <CopeBox>
        <Box
          sx={{
            fontSize: matches ? 16 : 14,
            whiteSpace: 'nowrap',
            fontWeight: 'bold',
            color: '#8A899E',
            width: matches ? 220 : 180
          }}
        >
          {`Invitation code:`}
        </Box>
        <Typography
          onClick={() => setCopied(`${window.btoa(account ?? '')}`)}
          sx={{
            borderRadius: '25px',
            border: '1px #C5C4D6 solid',
            padding: matches ? '15px 5px' : '10px 5px',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            fontSize: matches ? 16 : 14,
            whiteSpace: 'nowrap',
            fontWeight: 'bold',
            width: 'calc(100%)',
            color: '#8A899E'
          }}
        >
          {`${window.btoa(account ?? '')}`}
        </Typography>
        <Button
          onClick={() => setCopiedCode(`${window.btoa(account ?? '')}`)}
          sx={{
            color: 'white',
            backgroundColor: '#0167BC',
            height: matches ? 50 : 36,
            borderRadius: '25px',
            width: '80px',
            padding: '0 30px',
            fontSize: matches ? 16 : 14,
            ':hover': {
              backgroundColor: '#0167BC'
            }
          }}
        >
          {isCopiedCode ? t('copied') : t('copy')}
        </Button>
      </CopeBox>
    </Modal>
  )
}
