interface TabContent {
  title: string
  route?: string
  link?: string
  titleContent?: JSX.Element
  icon?: string
  comingSoon?: boolean
  disable?: boolean
}

export interface Tab extends TabContent {
  subTab?: TabContent[]
}

export function useTabs(): Tab[] {
  const { mintStartTime, allOpenTime } = config
  const timer = useRef<any>()
  const { t } = useI18n()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const disableTabs = [
    { title: t('home'), link: 'https://lynk.im', icon: home, disable: false },
    { title: t('fledglingTitle'), route: routes.Fledgling, icon: build, disable: false },
    { title: t('marketTitle'), route: routes.market, icon: market, disable: true },
    { title: t('profileTitle'), route: routes.profile.index, icon: user, disable: true },
    { title: t('rankTitle'), route: routes.Ranking, icon: king, disable: true },
    {
      title: t('more'),
      icon: more,
      disable: true,
      subTab: [
        { title: t('chatRoom'), comingSoon: true, disable: true },
        { title: t('alliance'), comingSoon: true, disable: true },
        { title: t('ns'), comingSoon: true, disable: true },
        { title: t('feed'), comingSoon: true, disable: true }
      ]
    }
  ]
  const enableTabs = disableTabs.map(item => {
    if (item?.subTab) {
      const subTab = item?.subTab.map(el => {
        return { ...el, disable: false }
      })
      return { ...item, subTab: subTab, disable: false }
    } else {
      return { ...item, disable: false }
    }
  })
  const someDisableTabs = disableTabs.map(item => {
    if (item?.title === t('profileTitle')) {
      return { ...item, disable: false }
    } else {
      return item
    }
  })
  const [tabs, setTabs] = useState(disableTabs)

  useEffect(() => {
    timer.current = setInterval(() => {
      if (moment(new Date()).valueOf() > allOpenTime) {
        setTabs(enableTabs)
        clearInterval(timer.current)
      } else if (moment(new Date()).valueOf() > mintStartTime && moment(new Date()).valueOf() < allOpenTime) {
        setTabs(someDisableTabs)
      } else {
        setTabs(disableTabs)
      }
    }, 1000)
    return () => clearInterval(timer.current)
  }, [allOpenTime, disableTabs, enableTabs, mintStartTime, someDisableTabs])

  return tabs
}

