import React from 'react'
import { ButtonBase, Theme, useTheme } from '@mui/material'
import { SxProps } from '@mui/system'

interface Props {
  onClick?: ((e: React.MouseEvent<HTMLButtonElement>) => void) | (() => void)
  width?: string
  height?: string
  backgroundColor?: string
  disabled?: boolean
  color?: string
  children?: React.ReactNode
  fontSize?: string | number
  classname?: string
  style?: React.CSSProperties & SxProps<Theme>
  active?: boolean
  disableRipple?: boolean
}

export default function Button(props: Props) {
  const { onClick, disabled, style, width, height, fontSize, backgroundColor, color, disableRipple } = props
  const theme = useTheme()
  return (
    <ButtonBase
      disableRipple={disableRipple}
      onClick={onClick}
      disabled={disabled}
      sx={{
        padding: '6px 13px',
        width: width || 'fit-content',
        height: height || 30,
        fontSize: fontSize || 14,
        fontWeight: 500,
        transition: '.3s',
        borderRadius: `${theme.shape.borderRadius}px`,
        backgroundColor: backgroundColor || theme.palette.primary.main,
        color: color || theme.palette.primary.contrastText,
        '&:hover': {
          opacity: 0.8
        },
        '&:disabled': {
          opacity: 0.5
        },
        ...style
      }}
    >
      {props.children}
    </ButtonBase>
  )
}

export function BlackButton({ style, ...props }: Props) {
  const theme = useTheme()
  return (
    <Button
      {...props}
      style={{
        backgroundColor: theme.palette.primary.main,
        '&:hover': {
          opacity: 0.8
        },
        '&:disabled': {
          backgroundColor: '#B9B9B9'
        },
        ...style
      }}
    />
  )
}

export function WhiteButton(props: Props) {
  const { onClick, disabled, style, width, height, fontSize, backgroundColor, color, disableRipple } = props
  const theme = useTheme()
  return (
    <ButtonBase
      disableRipple={disableRipple}
      onClick={onClick}
      disabled={disabled}
      sx={{
        padding: '6px 13px',
        width: width || 'fit-content',
        height: height || 30,
        fontSize: fontSize || 12,
        fontWeight: 500,
        transition: '.3s',
        border: '1px solid #F1F0F2',
        borderRadius: `${theme.shape.borderRadius}px`,
        backgroundColor: backgroundColor || '#FFFFFF',
        color: color || '#141420',
        '&:hover': {
          opacity: 0.8
        },
        '&:disabled': {
          opacity: 0.5
        },
        ...style
      }}
    >
      {props.children}
    </ButtonBase>
  )
}

export function DefaultButton({ style, active, ...props }: Props) {
  const theme = useTheme()

  return (
    <Button
      {...props}
      style={{
        color: active ? theme.palette.text.secondary : theme.palette.grey.A200,
        backgroundColor: active ? theme.palette.primary.main : theme.palette.primary.light,
        borderRadius: '35px',
        height: '70px',
        '&:hover': {
          opacity: 0.8
        },
        ...style
      }}
    />
  )
}

export function BlueButton({ style, active, ...props }: Props) {
  const theme = useTheme()

  return (
    <Button
      {...props}
      style={{
        color: active ? theme.palette.text.primary : theme.palette.text.disabled,
        height: '40px',
        width: '160px',
        background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)',
        borderRadius: '35px',
        '&:hover': {
          opacity: 0.8
        },
        ...style
      }}
    />
  )
}

export function LineButton({ style, ...props }: Props) {
  const theme = useTheme()

  return (
    <Button
      {...props}
      style={{
        width: 'auto',
        color: theme.palette.primary.main,
        height: '49px',
        background: 'rgba(0,0,0,0)',
        borderRadius: '35px',
        border: `1px solid ${theme.palette.primary.main}`,
        '&:hover': {
          background: theme.palette.primary.main
        },
        ...style
      }}
    />
  )
}
