import Modal from '../index'
import { Stack, Typography } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import ActionButton from '../../Button/ActionButton'
import { useCallback, useMemo, useState } from 'react'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import { ApprovalState, useApproveCallback } from '../../../hooks/useApproveCallback'
import { LYNK, LRT, SWAP_ADDRESS } from '../../../constants'
import useModal from '../../../hooks/useModal'
import { TokenAmount } from '../../../constants/token'
import { tryParseAmount } from '../../../utils/parseAmount'
import { useSwap } from '../../../hooks/useSwap'
import CurrencyInputPanel from '../../Input/CurrencyInputPanel'
import arrowLeft from 'assets/images/arrow_left.png'
import Image from '../../Image'
import { BetweenRow } from '../../row'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { useActiveWeb3React } from '../../../hooks'

export default function SwapModal({ isOpen, onDismiss }: { isOpen: boolean; onDismiss: () => void }) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const { showModal, hideModal } = useModal()
  const [typed, setTyped] = useState('')
  const { swap, price } = useSwap()

  const swapAmount = useMemo(() => {
    return typed ? (tryParseAmount(typed, LRT) as TokenAmount) : undefined
  }, [typed])

  const lynkBalance = useTokenBalance(account ?? undefined, LRT)

  const enoughAsset = swapAmount && lynkBalance && !lynkBalance.lessThan(swapAmount)

  const [approvalState, approveCallback] = useApproveCallback(swapAmount, SWAP_ADDRESS)

  const makeCallback = useCallback(async () => {
    if (!swapAmount) return
    showModal(<TransactionPendingModal />)
    swap(swapAmount)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
        onDismiss()
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [swapAmount, showModal, swap, hideModal, onDismiss])

  return (
    <Modal customIsOpen={isOpen} customOnDismiss={onDismiss} isCardOnMobile title={`${t('swapALYX')}`} closeIcon>
      <Stack width={'100%'} spacing={20} alignItems={'center'} padding={30}>
        <CurrencyInputPanel
          onMax={() => setTyped(lynkBalance?.toSignificant().toString() ?? '')}
          currency={LRT}
          value={typed}
          onChange={e => setTyped(e.target.value)}
        />
        <Image width={'20px'} style={{ transform: 'rotate(-90deg)' }} src={arrowLeft} />
        <CurrencyInputPanel
          placeholder="receive amount"
          disabled
          currency={LYNK}
          value={price && swapAmount ? swapAmount.multiply(price)?.toFixed(6, { groupSeparator: ',' }) : '--'}
          onChange={() => {}}
        />
        <BetweenRow>
          <Typography>{t('swapRate')}</Typography>
          <Typography>1 LRT = {price ? price.toSignificant() : '--'} LYNK</Typography>
        </BetweenRow>
        <Stack height={'fit-content'} width={'100%'} spacing={20}>
          <ActionButton
            disableAction={!swapAmount || !lynkBalance || !enoughAsset}
            pending={approvalState === ApprovalState.PENDING}
            pendingText={t('approving')}
            style={{ height: 50, width: '100%', background: '#141420' }}
            onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : makeCallback}
            actionText={
              approvalState === ApprovalState.NOT_APPROVED
                ? t('approve')
                : swapAmount && lynkBalance && !enoughAsset
                ? t('insufficient')
                : t('swap')
            }
          />
        </Stack>
      </Stack>
    </Modal>
  )
}
