import { HelpOutlineOutlined } from '@mui/icons-material'
import { Box, Grid, Stack, styled, Tooltip, Typography } from '@mui/material'
import Profile from '.'
import inviter1 from 'assets/images/inviter1.png'
import inviter2 from 'assets/images/inviter2.png'
import inviter3 from 'assets/images/inviter3.png'
import inviter4 from 'assets/images/inviter4.png'
import inviter5 from 'assets/images/inviter5.png'
import invite_user from 'assets/images/invite_user.png'
import OutlineButton from 'components/Button/OutlineButton'
import Button from 'components/Button/Button'
import { SimpleProgress } from 'components/Progress'
import useBreakpoint from 'hooks/useBreakpoint'
import { useCallback, useMemo, useState } from 'react'
import { useUserInfo, useValidAddress } from '../../hooks/useUser'
import { CurrencyAmount } from '../../constants/token'
import JSBI from 'jsbi'
import { useParams } from 'react-router-dom'
import { useActiveWeb3React } from '../../hooks'
import TipModal from '../../components/Modal/base/TipModal'
import RegisterModal from '../../components/Modal/base/RegisterModal'
import InviteModal from '../../components/Modal/base/InviteModal'
import useModal from '../../hooks/useModal'
import { useI18n } from 'react-simple-i18n'
import TransactionPendingModal from '../../components/Modal/TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../../components/Modal/TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../../components/Modal/TransactionModals/MessageBox'
import { TitleHelper } from './TitleHelper'
import BaseTooltip from '../../components/Tooltip'

const StyledItemWrapper = styled(Box)({
  borderRadius: '10px',
  padding: '20px 30px',
  background: 'linear-gradient(278deg, #2A80F5 0%, #5EAEFF 100%)',
  height: 160,
  position: 'relative',
  '& p': {
    color: '#fff'
  }
})

const StyledBetween = styled(Box)({
  display: 'flex',
  justifyItems: 'center',
  justifyContent: 'space-between'
})

const TabWrapper = styled(Box)({
  display: 'inline-flex',
  overflowX: 'auto',
  width: '100%',
  '&::-webkit-scrollbar': {
    display: 'none'
  },
  '& p': {
    cursor: 'pointer',
    fontSize: 16,
    padding: '10px 0',
    marginRight: 20,
    borderBottom: '1px solid transparent',
    '&.active': {
      fontWeight: 500,
      borderBottom: '1px solid #0167BC'
    }
  }
})

function CardItem({
  title,
  about,
  value1,
  value2,
  text1,
  text2,
  bg,
  image,
  onShowModal,
  tooltipTitle
}: {
  title: string
  about: string
  value1: string
  value2: string
  text1: string
  text2: string
  bg: string
  image: string
  onShowModal: () => void
  tooltipTitle: string
}) {
  return (
    <Grid item lg={2} sm={5} xs={10}>
      <StyledItemWrapper
        sx={{
          background: bg
        }}
      >
        <Box display={'flex'} alignItems="center">
          <Typography mr={4} fontSize={16} fontWeight={500}>
            {title}
          </Typography>
          <BaseTooltip title={tooltipTitle} placement="top">
            <HelpOutlineOutlined onClick={onShowModal} sx={{ color: '#fff' }} />
          </BaseTooltip>
        </Box>
        <Box mt={40} display="grid" gridTemplateColumns={'1fr 1fr'} justifyItems="center" gap={15}>
          <Box>
            <Typography fontSize={24} textAlign="center" fontWeight={500}>
              {value1}
            </Typography>
            <Typography fontSize={12} textAlign="center">
              {text1}
            </Typography>
          </Box>
          <Box>
            <Typography fontSize={24} textAlign="center" fontWeight={500}>
              {value2}
            </Typography>
            <Typography fontSize={12} textAlign="center">
              {text2}
            </Typography>
          </Box>
        </Box>
        <img
          src={image}
          style={{
            height: 108,
            position: 'absolute',
            right: 10,
            bottom: 0
          }}
        />
      </StyledItemWrapper>
    </Grid>
  )
}

export default function Inviter() {
  const isMdDown = useBreakpoint('md')
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const { showModal, hideModal } = useModal()
  const params = useParams<{ inviter: string }>()
  const inviter = params.inviter ? window.atob(params.inviter) : undefined
  const validAddress = useValidAddress(inviter)
  const mintBle = useValidAddress(account ?? undefined)
  const [selectedLevel, setSelectedLevel] = useState(0)
  const userInfo = useUserInfo()
  const registered = useValidAddress(account ?? undefined)
  const currenInviterNum =
    selectedLevel === 0
      ? 0
      : userInfo.inviterOfLevel[selectedLevel - 1]?.result
      ? userInfo.inviterOfLevel[selectedLevel - 1]?.result?.[0]
      : 0

  const currenInviterMax =
    selectedLevel === 0
      ? 0
      : userInfo.inviterMaxOfLevel[selectedLevel - 1]?.result
      ? userInfo.inviterMaxOfLevel[selectedLevel - 1]?.result?.[0]
      : 0

  const currenCaMax =
    selectedLevel === 0
      ? 0
      : userInfo.caMaxOfLevel[selectedLevel - 1]?.result
      ? userInfo.caMaxOfLevel[selectedLevel - 1]?.result?.[0]
      : 0

  const levelList = [
    { title: t('elite'), rate: '5%', level: '1', rewardAP: '1', stakeAP: '1', invitationMax: 0, caMax: 0 },
    { title: t('epic'), rate: '6%', level: '3', rewardAP: '2', stakeAP: '2', invitationMax: 1, caMax: 10000 },
    { title: t('master'), rate: '7%', level: '5', rewardAP: '3', stakeAP: '4', invitationMax: 1, caMax: 50000 },
    { title: t('legendary'), rate: '8%', level: '10', rewardAP: '4', stakeAP: '6', invitationMax: 1, caMax: 100000 },
    { title: t('mythic'), rate: '9%', level: '15', rewardAP: '5', stakeAP: '8', invitationMax: 1, caMax: 300000 },
    { title: t('divine'), rate: '10%', level: '20', rewardAP: '6', stakeAP: '10', invitationMax: 1, caMax: 1000000 }
  ]

  const btnStyle = useMemo(() => {
    return isMdDown
      ? { width: '100%', height: '40px', borderRadius: '25px' }
      : { width: '120px', height: '40px', borderRadius: '25px' }
  }, [isMdDown])

  const upgradeCallback = useCallback(async () => {
    if (!userInfo) return
    showModal(<TransactionPendingModal />)
    userInfo
      .upgrade()
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [userInfo, showModal, hideModal])

  return (
    <Profile>
      <Box
        mb={30}
        display={'flex'}
        flexDirection={'column'}
        sx={{ flexDirection: { xs: 'column-reverse', sm: 'column' }, margin: { xs: '0 0 30px 0', sm: '30px 0' } }}
      >
        <Grid container columns={10} spacing={20}>
          <CardItem
            onShowModal={() => {}}
            bg="linear-gradient(278deg, #2A80F5 0%, #5EAEFF 100%)"
            title={t('inviterReward')}
            about=""
            value1={`${
              userInfo?.socialRewardRates
                ? CurrencyAmount.ether(
                    JSBI.multiply(JSBI.BigInt(userInfo.socialRewardRates.toString()), JSBI.BigInt(100))
                  ).toFixed(2, { groupSeparator: ',' })
                : '--'
            } %`}
            text1={t('rewardRate')}
            text2={`Total LRT`}
            value2={`${
              userInfo?.socialRev ? CurrencyAmount.ether(userInfo.socialRev).toFixed(2, { groupSeparator: ',' }) : '--'
            }`}
            image={inviter1}
            tooltipTitle={t('socialRewardTooltip')}
          />
          <CardItem
            onShowModal={() => {}}
            bg="linear-gradient(111deg, #ACDB54 0%, #5BC401 100%)"
            title={t('CommunityReward')}
            about=""
            value1={`${userInfo?.communityRewardRatesNumByLevel?.toString() ?? '--'}`}
            text1={t('rewardGeneration')}
            text2={`Total LRT`}
            value2={`${
              userInfo?.communityRev
                ? CurrencyAmount.ether(userInfo.communityRev).toFixed(2, { groupSeparator: ',' })
                : '--'
            }`}
            image={inviter2}
            tooltipTitle={t('communityRewardTooltip')}
          />
          <CardItem
            onShowModal={() => {}}
            bg="linear-gradient(309deg, #614DF6 0%, #A66FFF 100%)"
            title={t('contributeReward')}
            about=""
            value1={userInfo?.contributionOf?.toString() ?? '--'}
            text1={`${t('totalAdd')} CA`}
            text2={`Total AP`}
            value2={`${
              userInfo?.contributionRev
                ? CurrencyAmount.ether(userInfo.contributionRev).toFixed(2, { groupSeparator: ',' })
                : '--'
            } `}
            image={inviter3}
            tooltipTitle={t('contributeRewardTooltip')}
          />
          <CardItem
            onShowModal={() => {}}
            bg="linear-gradient(134deg, #FFB55E 0%, #DE671D 100%)"
            title={t('achievementReward')}
            about=""
            value1={userInfo?.achievementCounter?.toString() ?? '--'}
            text1={t('totalCycle')}
            text2={`Total AP`}
            value2={`${
              userInfo?.achievementRev
                ? CurrencyAmount.ether(userInfo.achievementRev).toFixed(2, { groupSeparator: ',' })
                : '--'
            }`}
            image={inviter4}
            tooltipTitle={t('achievementRewardTooltip')}
          />
          <CardItem
            onShowModal={() => {}}
            bg="linear-gradient(132deg, #DF57FF 0%, #B803D9 100%)"
            title={t('stakingReward')}
            about=""
            value1={userInfo?.stakedCount?.toString() ?? '--'}
            text1={t('stakingNFTAmount')}
            text2={`Total LRT`}
            value2={`${
              userInfo?.stakeRev ? CurrencyAmount.ether(userInfo.stakeRev).toFixed(2, { groupSeparator: ',' }) : '--'
            }`}
            image={inviter5}
            tooltipTitle={t('stakingRewardTooltip')}
          />
        </Grid>
        <Box
          sx={{
            marginTop: { xs: 0, sm: 40 },
            padding: { xs: 0, md: '0 20px' },
            border: { xs: 'none', md: '1px solid #F1F0F2' },
            borderRadius: '10px'
          }}
        >
          <Box
            padding="10px 0"
            display={'flex'}
            alignItems="center"
            justifyContent={'space-between'}
            sx={{
              borderBottom: '1px solid #F1F0F2'
            }}
          >
            <Box display={'flex'} alignItems="center">
              <Typography mr={4} fontSize={18} fontWeight={500}>
                {t('myLevel')}
              </Typography>
              <Tooltip title={''}>
                <HelpOutlineOutlined sx={{ color: '#fff' }} />
              </Tooltip>
            </Box>
            <OutlineButton
              onClick={() => {
                if (!mintBle) {
                  if (!inviter) {
                    showModal(<RegisterModal />)
                  } else if (validAddress) {
                    showModal(<RegisterModal inviter={inviter} />)
                  } else {
                    showModal(<TipModal tip={t('unValidLink')} />)
                  }
                } else {
                  showModal(<InviteModal router={'fledgling'} />)
                }
              }}
              height={35}
              width="130px"
              style={{
                borderColor: '#0167BC',
                color: '#0167BC'
              }}
            >
              <img src={invite_user} width={16} style={{ marginRight: 5 }} />
              {t('inviteFriend')}
            </OutlineButton>
          </Box>

          <TabWrapper>
            {levelList.map(({ title }, index) => {
              return (
                <Typography
                  onClick={() => setSelectedLevel(index)}
                  key={title}
                  className={selectedLevel === index ? 'active' : ''}
                >
                  {title}
                </Typography>
              )
            })}
          </TabWrapper>

          <Box
            sx={{
              margin: '25px 0',
              display: 'grid',
              gridTemplateColumns: { xs: 'unset', md: '460fr 1fr 405fr 1fr 100fr' },
              gap: { lg: 70, md: 20, xs: 30 },
              alignItems: 'center'
            }}
          >
            <Box display={'grid'} gap="20px">
              <SimpleProgress
                rule={
                  selectedLevel === 0
                    ? `${t('nourishInviter')}`
                    : `${t('nourishInviter')} ${currenInviterMax} ${levelList[selectedLevel - 1].title}`
                }
                val={currenInviterNum ?? 0}
                total={currenInviterMax}
                width="100%"
              />
              <SimpleProgress
                rule={selectedLevel === 0 ? `${t('teamGrade')} CA` : `${t('teamGrade')} => ${currenCaMax} CA`}
                val={userInfo.currenCaValue ?? 0}
                total={currenCaMax}
                width="100%"
              />
            </Box>
            {!isMdDown && <Box height={120} sx={{ background: '#F1F0F2' }}></Box>}
            <Stack
              spacing={10}
              sx={{
                '& p': {
                  fontWeight: 600
                }
              }}
            >
              <Typography fontWeight={600}>{t('levelPrivilege')}</Typography>
              <StyledBetween>
                <Typography color={'#8A899E'}>{t('inviteRewardRate')} </Typography>
                <Typography maxWidth={'40%'}>{levelList[selectedLevel].rate}</Typography>
              </StyledBetween>
              <StyledBetween>
                <Typography color={'#8A899E'}>{t('socialRewards')} </Typography>
                <Typography maxWidth={'40%'}>
                  {levelList[selectedLevel].level}
                  {t('generation')}
                </Typography>
              </StyledBetween>
              <StyledBetween>
                <Typography color={'#8A899E'}>{t('contributionReward')} </Typography>
                <TitleHelper text={`${t('inviteRule')} ${levelList[selectedLevel].rewardAP} AP`} />
              </StyledBetween>
              <StyledBetween>
                <Typography color={'#8A899E'}>{t('achievementReward')} </Typography>
                <TitleHelper text={`${t('NFTStakingRule')} ${levelList[selectedLevel].stakeAP} AP`} />
              </StyledBetween>
            </Stack>
            {!isMdDown && <Box height={120} sx={{ background: '#F1F0F2' }}></Box>}
            <Stack spacing={5}>
              {!registered && <Button style={{ ...btnStyle, background: '#8A899E' }}>{t('inactivated')}</Button>}
              {userInfo &&
                registered &&
                (!(Number(userInfo.level) < selectedLevel) ||
                  (selectedLevel === 0 && Number(userInfo.level) === 0)) && (
                  <Button style={{ ...btnStyle }}>{t('activated')}</Button>
                )}
              {userInfo && Number(userInfo.level) < selectedLevel && (
                <>
                  {!userInfo ||
                  Number(currenInviterNum) < Number(currenInviterMax) ||
                  Number(userInfo.currenCaValue) < Number(currenCaMax) ? (
                    <Button style={{ ...btnStyle, background: '#8A899E' }}>{t('inactivated')}</Button>
                  ) : (
                    <Button
                      onClick={upgradeCallback}
                      style={{ ...btnStyle, background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)' }}
                    >
                      {t('activation')}
                    </Button>
                  )}
                </>
              )}
            </Stack>
          </Box>
        </Box>
      </Box>
    </Profile>
  )
}
