import { MenuItem, Stack, Typography } from '@mui/material'
import Select from '../../Select/Select'
import { SUPPORTED_TOKEN, SUPPORTED_TOKEN_LIST, TOKEN, USDT } from '../../../constants'
import { useState } from 'react'
import InputNumerical from '../InputNumerical'
import { Token } from '../../../constants/token'

export default function AssetInputPanel({
  onAssetSelect,
  onTyped
}: {
  onAssetSelect: (selectedToken: Token) => void
  onTyped: (value: string) => void
}) {
  const [selectedToken, setSelectedToken] = useState<Token>(USDT)
  const [typed, setTyped] = useState('')
  return (
    <Stack
      direction={'row'}
      sx={{
        height: 50,
        width: { xs: '100%', sm: 346 },
        margin: 'auto',
        border: '1px solid #C5C4D6',
        borderRadius: 25,
        overflow: 'hidden',
        marginTop: 20,
        background: '#FAFAFA'
      }}
    >
      <InputNumerical
        onChange={e => {
          onTyped(e.target.value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'))
          setTyped(e.target.value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'))
        }}
        value={typed}
        style={{ color: '#141420', fontWeight: 'bold', border: 'none', paddingLeft: 25 }}
      />
      <Stack width={2} height={'100%'} sx={{ backgroundColor: '#C5C4D6' }} />
      <Select
        style={{ border: 'none' }}
        width={100}
        height={50}
        value={selectedToken.symbol}
        onChange={e => {
          onAssetSelect(SUPPORTED_TOKEN[e.target.value as TOKEN])
          setSelectedToken(SUPPORTED_TOKEN[e.target.value as TOKEN])
        }}
      >
        {SUPPORTED_TOKEN_LIST.map(token => {
          return (
            <MenuItem key={token.address} value={token.symbol}>
              <Typography>{token.symbol}</Typography>
            </MenuItem>
          )
        })}
      </Select>
    </Stack>
  )
}
