import { Box, MenuItem, styled, Typography } from '@mui/material'
import Select from 'components/Select/Select'
import Table from 'components/Table'
import useBreakpoint from 'hooks/useBreakpoint'
import { useState } from 'react'
import Profile from '.'
import { useI18n } from 'react-simple-i18n'
import { useActiveWeb3React } from '../../hooks'
import { ArrowBack, ArrowForward } from '@mui/icons-material'
import { useHistory } from '../../hooks/useHistory'

const StyledTabs = styled(Box)({
  display: 'flex',
  flexWrap: 'wrap'
  // gridTemplateColumns: 'repeat(auto-fill, auto)',
  // gap: 20
})
const StyledTabItem = styled(Box)({
  height: 40,
  borderRadius: '27px',
  color: '#141420',
  background: '#F1F0F2',
  cursor: 'pointer',
  display: 'flex',
  padding: '10px',
  margin: '5px 10px',
  justifyContent: 'center',
  alignItems: 'center',
  '&.active': {
    color: '#fff',
    background: '#141420',
    fontWeight: 500
  }
})
const StyledMobileTabs = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between'
})

export default function History() {
  const size = 10
  const [curTab, setCurTab] = useState(0)
  const isSmDown = useBreakpoint('sm')
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const { page, historyList } = useHistory(account, curTab, size)

  const TABS = [
    t('profile.history.mint'),
    t('profile.history.trade'),
    t('profile.history.recommended'),
    t('profile.history.reward'),
    t('profile.history.socialReward'),
    t('profile.history.contribution'),
    t('profile.history.achievement'),
    t('profile.history.stack')
  ]

  return (
    <Profile>
      <Box margin={isSmDown ? '20px 0 30px 0' : '30px 0'}>
        {isSmDown ? (
          <StyledMobileTabs>
            {isSmDown ? (
              <div />
            ) : (
              <Typography fontSize={16} fontWeight={600}>
                {TABS[curTab]}
              </Typography>
            )}
            <Select
              height={34}
              style={{
                border: '1px solid #C5C4D6',
                borderRadius: '26px'
              }}
              value={curTab}
              onChange={e => setCurTab(e.target.value)}
              placeholder=""
            >
              {TABS.map((item, index) => (
                <MenuItem key={index} value={index} selected={index === curTab}>
                  {item}
                </MenuItem>
              ))}
            </Select>
          </StyledMobileTabs>
        ) : (
          <StyledTabs>
            {TABS.map((item, index) => (
              <StyledTabItem onClick={() => setCurTab(index)} key={index} className={curTab === index ? 'active' : ''}>
                {item}
              </StyledTabItem>
            ))}
          </StyledTabs>
        )}

        <Box mt={20}>
          {(function() {
            let header = ['']
            switch (curTab) {
              case 0:
                header = [t('time'), t('name'), t('amount'), 'TX']
                break
              case 1:
                header = [t('time'), 'NFT', t('price'), t('buyerAddress'), t('sellerAddress'), 'TX']
                break
              case 2:
                header = [t('time'), t('address'), 'TX']
                break
              case 3:
                header = [t('time'), t('address'), t('reward'), 'TX']
                break
              case 4:
                header = [t('time'), t('address'), t('reward'), 'TX']
                break
              case 5:
                header = [t('time'), t('address'), /*t('add'),*/ t('reward'), 'TX']
                break
              case 6:
                header = [t('time'), 'NFT', t('reward'), 'TX']
                break
              case 7:
                header = [t('time'), t('reward'), 'TX']
                break
            }
            return (
              <Box display={'flex'} flexDirection={'column'}>
                <Table rows={historyList} header={header} />
                <Paging page={page} />
              </Box>
            )
          })()}
        </Box>
      </Box>
    </Profile>
  )
}

export function Paging({ page }: { page: any }) {
  return (
    <Box mt={20} display="flex" justifyContent="end">
      <Box
        display={'flex'}
        alignItems="center"
        sx={{
          '&>*': {
            ml: '10px !important',
            cursor: 'pointer'
          },
          '& .disabled': {
            opacity: 0.5,
            cursor: 'not-allowed'
          }
        }}
      >
        <ArrowBack
          onClick={() => {
            if (page.hasPrev) {
              page.setCurrentPage(page.currentPage - 1)
            }
          }}
          className={page.hasPrev ? '' : 'disabled'}
        />
        <Typography fontSize={20}>{page.currentPage}</Typography>
        <ArrowForward
          onClick={() => {
            if (page.hasNext) {
              page.setCurrentPage(page.currentPage + 1)
            }
          }}
          className={page.hasNext ? '' : 'disabled'}
        />
      </Box>
    </Box>
  )
}
