import Modal from '../index'
import { Stack, Typography } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import { ModalCover } from '../base/NFT'
import ActionButton from '../../Button/ActionButton'
import { useCallback, useMemo, useState } from 'react'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import { ApprovalState } from '../../../hooks/useApproveCallback'
import { LYNKNFT_ADDRESS, MARKET_ADDRESS, USDT } from '../../../constants'
import useModal from '../../../hooks/useModal'
import { useMarket } from '../../../hooks/useMarket'
import { useERC721ApproveAllCallback } from '../../../hooks/useERC721ApproveAllCallback'
import AssetInputPanel from '../../Input/AssetInputPanel'
import { TokenAmount } from '../../../constants/token'
import { tryParseAmount } from '../../../utils/parseAmount'

export default function MakeOrderModal({
  id,
  name,
  estimatePrice,
  isOpen,
  onDismiss
}: {
  id: string
  name: string
  estimatePrice: string
  isOpen: boolean
  onDismiss: () => void
}) {
  const { t } = useI18n()
  const { showModal, hideModal } = useModal()
  const [token, setToken] = useState(USDT)
  const [amount, setAmount] = useState('')

  const saleAmount = useMemo(() => {
    return token && amount ? (tryParseAmount(amount, token) as TokenAmount) : undefined
  }, [amount, token])

  const { make } = useMarket()

  const [approvalState, approveCallback] = useERC721ApproveAllCallback(LYNKNFT_ADDRESS, MARKET_ADDRESS)

  const makeCallback = useCallback(async () => {
    if (!saleAmount) return
    showModal(<TransactionPendingModal />)
    make(id, saleAmount)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
        onDismiss()
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [saleAmount, showModal, make, id, hideModal, onDismiss])

  return (
    <Modal
      customIsOpen={isOpen}
      customOnDismiss={onDismiss}
      width={'610px'}
      maxWidth={'610px'}
      isCardOnMobile
      title={`${t('sale')}`}
      closeIcon
    >
      <Stack
        direction={'row'}
        sx={{ margin: 20, flexDirection: { xs: 'column', sm: 'row' }, marginTop: { xs: 30 } }}
        spacing={20}
        alignItems={'center'}
      >
        <Stack spacing={10} width={'100%'}>
          <ModalCover id={id} />
          <Typography
            sx={{ fontSize: { xs: 20, md: 16 }, textAlign: { xs: 'center', md: 'left' } }}
            fontFamily={'swiss!important'}
            fontWeight={'900'}
          >
            {name}
          </Typography>
        </Stack>
        <Stack height={'fit-content'} width={'100%'} spacing={20}>
          <Typography sx={{ marginTop: { xs: 30, sm: 0 } }} fontSize={12} color={'#8A899E'} alignSelf={'center'}>
            {t('estimatePrice')} <span style={{ color: 'black' }}>{estimatePrice} U</span>
          </Typography>
          <AssetInputPanel
            onTyped={value => setAmount(value)}
            onAssetSelect={selectedToken => setToken(selectedToken)}
          />
          <ActionButton
            pending={approvalState === ApprovalState.PENDING}
            pendingText={t('approving')}
            style={{ height: 50, width: '100%', background: '#141420' }}
            onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : makeCallback}
            actionText={approvalState === ApprovalState.NOT_APPROVED ? t('approve') : t('confirm')}
          />
        </Stack>
      </Stack>
    </Modal>
  )
}
