import { ChangeEvent } from 'react'
import { styled, Box, useTheme, Typography } from '@mui/material'
import InputNumerical from 'components/Input/InputNumerical'
import OutlineButton from 'components/Button/OutlineButton'
import InputLabel from 'components/Input/InputLabel'
import LogoText from 'components/LogoText'
import { useActiveWeb3React } from 'hooks'
import { useCurrencyBalance } from 'state/wallet/hooks'
import { Currency } from 'constants/token/currency'

interface Props {
  currency?: Currency | null
  value: string
  onChange: (e: ChangeEvent<HTMLInputElement>) => void
  onMax?: () => void
  disabled?: boolean
  placeholder?: string
  inputFocused?: boolean
  customBalanceText?: string
  hideBalance?: boolean
}

const InputRow = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: '10px',
  padding: '0 20px',
  width: '100%',
  height: '48px',
  display: 'flex',
  justifyContent: 'flex-end',
  border: '1px solid #C5C4D6',
  [theme.breakpoints.down('md')]: {
    width: '100%'
  }
}))

const StyledInput = styled(InputNumerical)({
  position: 'absolute'
})

const ButtonWrapper = styled('div')(({ theme }) => ({
  position: 'absolute',
  height: '100%',
  display: 'flex',
  alignItems: 'center',
  [theme.breakpoints.down('md')]: {
    right: 20
  }
}))

export default function CurrencyInputPanel({
  onMax,
  value,
  disabled,
  placeholder,
  inputFocused,
  currency,
  customBalanceText,
  hideBalance,
  onChange
}: Props) {
  const { account } = useActiveWeb3React()
  const selectedCurrencyBalance = useCurrencyBalance(account ?? undefined, currency ?? undefined)
  const theme = useTheme()

  return (
    <Box display="grid" gap="24px" width={'100%'}>
      <div>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          <InputLabel>Amount</InputLabel>
          {currency && (
            <Typography color={theme.textColor.text3} fontWeight={500} fontSize={14}>
              {!hideBalance && !!currency && selectedCurrencyBalance
                ? (customBalanceText ?? 'Your balance: ') + selectedCurrencyBalance?.toFixed(2, { groupSeparator: ',' })
                : ' -'}
            </Typography>
          )}
        </Box>
        <InputRow>
          <StyledInput
            style={{ border: 'none' }}
            placeholder={placeholder ?? 'Enter amount'}
            value={value.toString()}
            onChange={onChange}
            type={'number'}
            disabled={disabled}
            focused={inputFocused}
          />
          {currency && onMax && (
            <ButtonWrapper>
              <OutlineButton
                style={{ marginRight: 46 }}
                width="64px"
                height="28px"
                onClick={onMax}
                color={theme.textColor.text1}
                borderRadius="20px"
              >
                Max
              </OutlineButton>
            </ButtonWrapper>
          )}
          {currency ? <LogoText text={currency.symbol} /> : <>Select Token</>}
        </InputRow>
      </div>
    </Box>
  )
}
