function renderTransactions(transactions: string[]) {
  return (
    <>
      {transactions.map((hash, i) => {
        return <Transaction key={i} hash={hash} />
      })}
    </>
  )
}

interface AccountDetailsProps {
  toggleWalletModal: () => void
  pendingTransactions: string[]
  confirmedTransactions: string[]
  ENSName?: string
  openOptions: () => void
}

export default function AccountDetails({
  toggleWalletModal,
  pendingTransactions,
  confirmedTransactions,
  openOptions
}: AccountDetailsProps) {
  const { chainId, account, connector, deactivate } = useActiveWeb3React()
  const dispatch = useDispatch<AppDispatch>()
  const theme = useTheme()
  const apBalance = useTokenBalance(account ?? undefined, AP)
  const lynkBalance = useTokenBalance(account ?? undefined, LRT)
  const usdtBalance = useTokenBalance(account ?? undefined, USDT)
  const alyxBalance = useTokenBalance(account ?? undefined, LYNK)

  function formatConnectorName() {
    const { ethereum } = window
    const isMetaMask = !!(ethereum && ethereum.isMetaMask)
    const name = Object.keys(SUPPORTED_WALLETS)
      .filter(
        k =>
          SUPPORTED_WALLETS[k].connector === connector && (connector !== injected || isMetaMask === (k === 'METAMASK'))
      )
      .map(k => SUPPORTED_WALLETS[k].name)[0]
    return (
      <Typography color={'black'} fontSize="0.825rem" fontWeight={500}>
        Connected with {name}
      </Typography>
    )
  }

  const clearAllTransactionsCallback = useCallback(() => {
    if (chainId) dispatch(clearAllTransactions({ chainId }))
  }, [dispatch, chainId])

  return (
    <>
      <Box display="grid" width="100%" gridTemplateRows="50px 20px 20px" gap="12px" marginBottom="20px">
        <Box
          justifyContent={'center'}
          color={'black'}
          display="flex"
          fontSize={24}
          fontWeight={500}
          gap="16px"
          width="100%"
          id="web3-account-identifier-row"
        >
          My Wallet
          {/*{connector && <Dot />}*/}
          {/*{ENSName ? <span> {ENSName}</span> : <span> {account && shortenAddress(account)}</span>}*/}
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          marginBottom="20px"
          color={theme.textColor.text3}
        >
          {formatConnectorName()}
          {/*{connector !== walletlink && connector?.deactivate && (*/}
          {/*  <Box*/}
          {/*    sx={{ ml: 8, cursor: 'pointer', background: '#edecec', padding: '2px 10px', borderRadius: '4px' }}*/}
          {/*    onClick={() => {*/}
          {/*      console.log(connector?.deactivate.toString())*/}
          {/*      connector?.deactivate()*/}
          {/*      deactivate()*/}
          {/*    }}*/}
          {/*  >*/}
          {/*    Disconnect*/}
          {/*  </Box>*/}
          {/*)}*/}
        </Box>

        <Box
          mt={20}
          display="flex"
          fontSize={18}
          fontWeight={500}
          gap="16px"
          width="100%"
          id="web3-account-identifier-row"
        >
          MY ADDRESS
          {/*{connector && <Dot />}*/}
          {/*{ENSName ? <span> {ENSName}</span> : <span> {account && shortenAddress(account)}</span>}*/}
        </Box>

        <Box mt={28} display="flex" width="100%" color={theme.textColor.text3}>
          {account && (
            <Copy toCopy={account}>
              <Typography sx={{ wordBreak: 'break-all', fontSize: 12 }} mr={10} variant="body2">
                {account}
              </Typography>
            </Copy>
          )}
        </Box>
      </Box>
      <Grid container spacing={10}>
        <Grid item xs={12}>
          <BetweenRow>
            <Typography>
              AP&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: {apBalance ? apBalance.toFixed(2, { groupSeparator: ',' }) : '--'}
            </Typography>
            <Typography
              fontSize={12}
              sx={{ textDecoration: 'underline', cursor: 'pointer' }}
              onClick={() => registerToken(AP.address, AP.symbol ?? '', AP.decimals, '')}
            >
              Add token
            </Typography>
          </BetweenRow>
        </Grid>
        <Grid item xs={12}>
          <BetweenRow>
            <Typography>
              LRT&nbsp;&nbsp;&nbsp;: {lynkBalance ? lynkBalance.toFixed(2, { groupSeparator: ',' }) : '--'}
            </Typography>{' '}
            <Typography
              fontSize={12}
              sx={{ textDecoration: 'underline', cursor: 'pointer' }}
              onClick={() => registerToken(LRT.address, LRT.symbol ?? '', LRT.decimals, '')}
            >
              Add token
            </Typography>
          </BetweenRow>
        </Grid>
        <Grid item xs={12}>
          <Stack direction={'row'} justifyContent={'space-between'}>
            <Typography>LYNK&nbsp;: {alyxBalance ? alyxBalance?.toFixed(2, { groupSeparator: ',' }) : '--'}</Typography>
            <Typography
              fontSize={12}
              sx={{ textDecoration: 'underline', cursor: 'pointer' }}
              onClick={() => registerToken(LYNK.address, LYNK.symbol ?? '', LYNK.decimals, '')}
            >
              Add token
            </Typography>
          </Stack>
        </Grid>
        <Grid item xs={12}>
          <Stack direction={'row'} justifyContent={'space-between'}>
            <Typography>USDT&nbsp;: {usdtBalance ? usdtBalance.toFixed(2, { groupSeparator: ',' }) : '--'}</Typography>
            <Typography
              fontSize={12}
              sx={{ textDecoration: 'underline', cursor: 'pointer' }}
              onClick={() => registerToken(USDT.address, USDT.symbol ?? '', USDT.decimals, '')}
            >
              Add token
            </Typography>
          </Stack>
        </Grid>
      </Grid>
      <Box mt={20} display="flex" gap="10px" width="100%" justifyContent="center">
        <OutlineButton
          width={'100%'}
          borderRadius="15px"
          onClick={() => {
            toggleWalletModal()
            deactivate()
          }}
          primary
        >
          Logout
        </OutlineButton>
        <Button
          width={'100%'}
          height="60px"
          onClick={() => {
            openOptions()
          }}
        >
          Change
        </Button>
      </Box>
      <OutlinedCard style={{ marginTop: 20, maxHeight: 150, overflowY: 'auto' }} width="100%" padding="20px">
        {!!pendingTransactions.length || !!confirmedTransactions.length ? (
          <Box display="grid" gap="16px" width="100%">
            <Box display="flex" justifyContent="space-between" width="100%" fontWeight={500}>
              <Typography variant="inherit">Recent Transactions</Typography>
              <TextButton onClick={clearAllTransactionsCallback}>(clear all)</TextButton>
            </Box>
            <Box display="grid">
              {renderTransactions(pendingTransactions)}
              {renderTransactions(confirmedTransactions)}
            </Box>
          </Box>
        ) : (
          <Box display="flex" width="100%" justifyContent="center" marginTop={1}>
            <Typography> Your transactions will appear here...</Typography>
          </Box>
        )}
      </OutlinedCard>
    </>
  )
}
