import { Attr, SUPPORT_ATTR } from '../../../utils/attr'
import Modal from '../index'
import { MenuItem, Stack, Typography, useTheme } from '@mui/material'
import Select from '../../Select/Select'
import { LYNKNFT_ADDRESS, SUPPORTED_TOKEN, TOKEN } from '../../../constants'
import ActionButton from '../../Button/ActionButton'
import InputNumerical from '../../Input/InputNumerical'
import { useCallback, useEffect, useState } from 'react'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { tryParseAmount } from '../../../utils/parseAmount'
import { useI18n } from 'react-simple-i18n'
import { useNFT, useVAInfo } from '../../../hooks/useNFT'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import useModal from '../../../hooks/useModal'
import { useActiveWeb3React } from '../../../hooks'
import { Token } from '../../../constants/token'
import { ApprovalState, useApproveCallback } from '../../../hooks/useApproveCallback'

export default function UpgradeModal({
  tokenId,
  tokenName,
  attr,
  title,
  tokens,
  max,
  isOpen,
  onDismiss,
  levelMax,
  value
}: {
  tokenId: string
  tokenName: string
  attr: Attr
  title: string
  tokens: Token[]
  max: string
  isOpen: boolean
  onDismiss: () => void
  levelMax: number
  value: number
}) {
  const { t } = useI18n()
  const theme = useTheme()
  const { account } = useActiveWeb3React()
  const { showModal, hideModal } = useModal()
  const [tokenTag, setTokenTag] = useState(tokens[0].symbol)
  const paymentToken = SUPPORTED_TOKEN[tokenTag as TOKEN]
  const [typed, setTyped] = useState('')
  const balance = useTokenBalance(account ?? undefined, paymentToken)
  const payAmount = tryParseAmount(typed, paymentToken)
  const enoughAsset = balance && payAmount && !balance.lessThan(payAmount)
  const [approvalState, approveCallback] = useApproveCallback(payAmount, LYNKNFT_ADDRESS)
  const { upgrade } = useNFT()
  const { loading, maxValue } = useVAInfo(tokenId)
  const maxValueNum = Number(maxValue.toString())
  useEffect(() => {
    setTokenTag(tokens[0].symbol)
  }, [tokens])
  console.log(max)
  const upgradeCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    upgrade(SUPPORT_ATTR[attr].attr, tokenId, tokenName, typed, paymentToken.address)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
        onDismiss()
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, upgrade, attr, tokenId, tokenName, typed, paymentToken.address, hideModal, onDismiss])
  return (
    <Modal customIsOpen={isOpen} customOnDismiss={onDismiss} title={title} closeIcon>
      <Stack
        direction={'row'}
        sx={{
          height: 50,
          width: 360,
          margin: 'auto',
          border: '1px solid #C5C4D6',
          borderRadius: 25,
          overflow: 'hidden',
          marginTop: 20,
          background: '#FAFAFA'
        }}
      >
        <InputNumerical
          placeholder={'0'}
          onMax={() =>
            setTyped(
              levelMax - value > 0
                ? (attr === 1 && levelMax - value > maxValueNum ? maxValueNum : levelMax - value).toString()
                : '0'
            )
          }
          style={{ color: theme.palette.primary.main, fontWeight: 'bold', paddingLeft: 25, border: 'none' }}
          color={'#141420'}
          width={260}
          onChange={e => {
            const eventValue = e.target.value.replace(/[^0-9]/g, '').replace(/\b(0+)/gi, '')
            setTyped(() =>
              attr === 0
                ? eventValue
                : attr === 1
                ? levelMax - value > maxValueNum
                  ? Number(eventValue) > maxValueNum
                    ? maxValueNum.toString()
                    : eventValue
                  : Number(eventValue) > levelMax - value
                  ? (levelMax - value).toString()
                  : eventValue
                : Number(eventValue) + value - levelMax > 0
                ? (levelMax - value).toString()
                : eventValue
            )
          }}
          value={typed}
        />

        <Stack width={2} height={'100%'} sx={{ backgroundColor: '#C5C4D6' }} />
        <Select
          style={{ border: 'none' }}
          width={100}
          height={50}
          value={tokenTag}
          onChange={e => {
            setTokenTag(e.target.value as TOKEN)
          }}
        >
          {tokens.map(({ symbol }) => {
            return (
              <MenuItem key={symbol} value={symbol}>
                <Typography>{symbol}</Typography>
              </MenuItem>
            )
          })}
        </Select>
      </Stack>
      <Stack
        direction={'row'}
        sx={{ width: 360, paddingTop: 10, margin: 'auto', justifyContent: 'flex-end', ' p': { fontSize: 12 } }}
      >
        <Typography color={'#8A899E'}>{t('availableBalance')}: </Typography>
        <Typography>{balance ? balance.toFixed(2, { groupSeparator: ',' }) : '--'}</Typography>
        <Typography marginLeft={4} color={'#8A899E'}>
          {balance?.token.symbol}
        </Typography>
      </Stack>
      <Stack sx={{ margin: '20px auto', width: 360 }}>
        <ActionButton
          pending={!balance || approvalState === ApprovalState.PENDING || loading}
          pendingText={!balance ? ' ' : t('approving')}
          disableAction={!enoughAsset}
          style={{
            width: 360,
            background: theme.palette.primary.main,
            height: theme.height.medium
          }}
          onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : upgradeCallback}
          actionText={
            !payAmount
              ? t('enterPoint')
              : !enoughAsset
              ? t('insufficient')
              : approvalState === ApprovalState.NOT_APPROVED
              ? t('approve')
              : t('confirm')
          }
        />
      </Stack>
    </Modal>
  )
}
