function InfoItem({ title, content, isLink }: { title: string; content: string; isLink?: string }) {
  const theme = useTheme()
  return (
    <BetweenRow>
      <Typography color={theme.textColor.text5}>{title}</Typography>
      {isLink ? (
        <ExternalLink href={content ? getEtherscanLink(ChainId.MAINNET, isLink, 'address') : ''}>
          <Typography color={theme.textColor.primary} fontWeight={'bold'}>
            {content}
          </Typography>
        </ExternalLink>
      ) : (
        <Typography fontWeight={'bold'}>{content}</Typography>
      )}
    </BetweenRow>
  )
}

function Progress({
  icon,
  title,
  current,
  max,
  onUpgrade,
  upgradeBle,
  levelMax,
  upgrading
}: {
  icon: string
  title: string
  current: number
  max: number
  upgradeBle: boolean
  onUpgrade: () => void
  levelMax: number
  upgrading: boolean
}) {
  const { t } = useI18n()
  let progress =
    current && levelMax
      ? JSBI.divide(JSBI.multiply(JSBI.BigInt(current), JSBI.BigInt(100)), JSBI.BigInt(levelMax)).toString()
      : '0'
  progress = Number(progress) > 100 ? '100' : progress
  return (
    <Stack width={'100%'} spacing={10}>
      <Stack direction={'row'} spacing={4}>
        <Image width={10} src={icon} />
        <Typography fontSize={14} fontWeight={'bold'} style={{ textTransform: 'capitalize' }}>
          {title}
        </Typography>
      </Stack>
      <Stack direction={'row'} alignItems={'center'}>
        <Stack sx={{ height: 30, width: '100%', position: 'relative', justifyContent: 'center' }}>
          <Box
            sx={{ position: 'absolute', height: 20, width: '100%', borderRadius: '10px', backgroundColor: '#f1f0f2' }}
          >
            &nbsp;
          </Box>
          <Typography
            sx={{
              width: `${progress}%`,
              height: 20,
              borderRadius: '15px',
              background: 'linear-gradient(90deg, #0167BC 0%, #5EAEFF 100%)',
              fontSize: 12,
              color: 'white',
              lineHeight: '20px',
              textAlign: 'right',
              position: 'absolute'
            }}
          >
            &nbsp;&nbsp;{Number.isNaN(current) ? '' : current.toString()}
            {!Number.isNaN(current) && levelMax?.toString() ? '/' : ''}
            {levelMax?.toString() ?? ''}&nbsp;&nbsp;
          </Typography>
        </Stack>
        {upgradeBle && (
          <IconButton
            disabled={(current === max && (title === 'intellect' || title === 'dexterity')) || upgrading}
            sx={{
              color: 'black',
              width: 118,
              alignSelf: 'end',
              border: '1px solid #8A899E',
              fontSize: 12,
              height: 40,
              borderRadius: 20,
              padding: '0 20px',
              marginLeft: 10
            }}
            onClick={onUpgrade}
          >
            {current === max && (title === 'intellect' || title === 'dexterity') ? null : upgrading ? (
              <Spinner marginRight={10} size={12} />
            ) : (
              <Accelerate />
            )}
            {current === max && (title === 'intellect' || title === 'dexterity') ? t('fullLevel') : t('up')}
          </IconButton>
        )}
      </Stack>
    </Stack>
  )
}

export default function NFTDetail() {
  const isSmDown = useBreakpoint('sm')
  const history = useHistory()
  const params = useParams<{ model: string; id: string }>()
  const tokenId = params.id
  const pageModel = params.model
  const theme = useTheme()
  const { account } = useActiveWeb3React()
  const blockNumber = useBlockNumber()
  const { t } = useI18n()
  //const { seller } = useOrder(params.id)
  const { showModal, hideModal } = useModal()
  const level = useLevel(tokenId)
  const { reward, duration, lastUpdateTime } = useReward(params.id)
  const { charisma, vitality, intellect, dexterity } = useAttr(tokenId)
  const recessiveAttrUri = useRecessiveAttrUri(tokenId)
  const { isMine, staked, listed, salePrice, saleToken } = useNFTStatus(tokenId)
  const { stake, withdraw } = useStaking()
  const { cancel } = useMarket()
  const [openTakeModal, setOpenTakeModal] = useState(false)
  const [openBuyPropsModal, setOpenBuyPropsModal] = useState(false)
  const [openMakeModal, setOpenMakeModal] = useState(false)
  const [recessiveAttr, setRecessiveAttr] = useState<{ trait_type: string; value: string }[]>([])
  const [openUpModal, setOpenUpModal] = useState(false)
  const [nowDate, setNowDate] = useState(moment(new Date()).valueOf())
  const [isCopied, setCopied] = useCopyClipboard()
  const { maxValue } = useVAInfo(tokenId)
  const maxValueNum = Number(maxValue.toString())
  const timer = useRef<any>()
  const allOpenTimerRef = useRef<any>()
  const { allOpenTime } = globalonfig
  const [showBtn, setShowBtn] = useState(moment(new Date()).valueOf() > allOpenTime)

  useEffect(() => {
    allOpenTimerRef.current = setInterval(() => {
      if (moment(new Date()).valueOf() > allOpenTime) {
        setShowBtn(true)
        clearInterval(allOpenTimerRef.current)
      } else {
        setShowBtn(false)
      }
    }, 1000)
    return () => clearInterval(allOpenTimerRef.current)
  }, [allOpenTime])

  useEffect(() => {
    console.log(recessiveAttrUri)
    if (recessiveAttrUri) {
      fetch(recessiveAttrUri)
        .then(res => res.clone().json())
        .then(json => {
          setRecessiveAttr(json?.attributes || [])
        })
    }
  }, [recessiveAttrUri])

  useEffect(() => {
    if (isMine && staked) {
      timer.current = setInterval(() => {
        setNowDate(moment(new Date()).valueOf())
      }, 1000)
    }
    return () => clearInterval(timer.current)
  }, [isMine, staked])

  const { submitted: staking } = useUserHasSubmitted(`${account}_staking_${tokenId}`)
  const { submitted: selling } = useUserHasSubmitted(`${account}_selling_${tokenId}`)
  const { submitted: withdrawing } = useUserHasSubmitted(`${account}_withdraw_${tokenId}`)
  const { submitted: canceling } = useUserHasSubmitted(`${account}_cancel_${tokenId}`)
  const { submitted: charismaUpgrade } = useUserHasSubmitted(`${account}_upgrade0_${tokenId}`)
  const { submitted: vitalityUpgrade } = useUserHasSubmitted(`${account}_upgrade1_${tokenId}`)
  const { submitted: intellectUpgrade } = useUserHasSubmitted(`${account}_upgrade2_${tokenId}`)
  const { submitted: dexterityUpgrade } = useUserHasSubmitted(`${account}_upgrade3_${tokenId}`)

  const [upData, setUpData] = useState({
    max: '',
    tokens: [USDT],
    tokenId: '',
    attr: Attr.CA,
    title: '',
    levelMax: 0,
    value: 0
  })

  const upgradeBle = isMine && !staked && !listed

  const func = useCallback(async () => {
    if (!params.id) return undefined
    return await getNFT(params.id)
  }, [params.id])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const nft: NFT | undefined = useAsyncMemo(func, undefined, [blockNumber])

  const priceAmount = useMemo(
    () => (nft?.priceInAcceptToken ? new TokenAmount(USDT, nft?.priceInAcceptToken) : undefined),
    [nft?.priceInAcceptToken]
  )

  const [approvalState, approveCallback] = useERC721ApproveAllCallback(LYNKNFT_ADDRESS, STAKING_ADDRESS)

  const stakeCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    stake(tokenId)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, stake, tokenId, hideModal])

  const withdrawCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    withdraw(tokenId)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, withdraw, tokenId, hideModal])

  const cancelCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    cancel(tokenId)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, cancel, tokenId, hideModal])

  const upgradeCallback = useCallback(
    (attr: Attr) => {
      if (attr === Attr.CA) {
        setUpData({
          max: JSBI.subtract(JSBI.BigInt(charisma.max), JSBI.BigInt(charisma.value)).toString(),
          tokens: [USDT, LRT],
          tokenId: tokenId,
          attr: Attr.CA,
          title: `CA ${t('upgrade')}`,
          levelMax: charisma.levelMax,
          value: charisma.value
        })
        setOpenUpModal(true)
      }
      if (attr === Attr.VA) {
        const { levelMax, value } = vitality
        if (maxValueNum && levelMax - value > 0) {
          setUpData({
            max: JSBI.subtract(JSBI.BigInt(vitality.max), JSBI.BigInt(vitality.value)).toString(),
            tokens: [AP],
            tokenId: tokenId,
            attr: Attr.VA,
            title: `VA ${t('upgrade')}`,
            levelMax: vitality.levelMax,
            value: vitality.value
          })
          setOpenUpModal(true)
        } else if (!maxValueNum) {
          showModal(<TipModal tip={`VA has been upgraded today`} />)
        } else {
          showModal(<TipModal tip={`Level ${t('upLimit')} ${Number(level.toString()) + 1}`} />)
        }
      }
      if (attr === Attr.IN) {
        if (charisma.levelMax > charisma.value) {
          showModal(<TipModal tip={`CA ${t('upLimit')} ${charisma.levelMax}`} />)
        } else if (vitality.levelMax > vitality.value) {
          showModal(<TipModal tip={`VA ${t('upLimit')} ${vitality.levelMax}`} />)
        } else if (intellect.value >= intellect.levelMax) {
          showModal(<TipModal tip={`Level ${t('upLimit')} ${Number(level.toString()) + 1}`} />)
        } else {
          setUpData({
            max: JSBI.subtract(JSBI.BigInt(intellect.max), JSBI.BigInt(intellect.value)).toString(),
            tokens: [AP],
            tokenId: tokenId,
            attr: Attr.IN,
            title: `IN ${t('upgrade')}`,
            levelMax: intellect.levelMax,
            value: intellect.value
          })
          setOpenUpModal(true)
        }
      }
      if (attr === Attr.DX) {
        if (intellect.levelMax > intellect.value) {
          showModal(<TipModal tip={`IN ${t('upLimit')} ${intellect.levelMax}`} />)
        } else {
          setUpData({
            max: JSBI.subtract(JSBI.BigInt(dexterity.max), JSBI.BigInt(dexterity.value)).toString(),
            tokens: [AP],
            tokenId: tokenId,
            attr: Attr.DX,
            title: `DX ${t('upgrade')}`,
            levelMax: dexterity.levelMax,
            value: dexterity.value
          })
          setOpenUpModal(true)
        }
      }
    },
    [
      charisma.levelMax,
      charisma.max,
      charisma.value,
      dexterity.levelMax,
      dexterity.max,
      dexterity.value,
      intellect.levelMax,
      intellect.max,
      intellect.value,
      level,
      maxValueNum,
      showModal,
      t,
      tokenId,
      vitality
    ]
  )

  const operation = useMemo(() => {
    return (
      <Box>
        <Stack
          direction={'row'}
          sx={{
            display: { xs: 'grid', sm: undefined },
            gridTemplateColumns: '1fr 1fr',
            backgroundColor: '#FAFAFA',
            height: 30,
            borderRadius: '3px',
            paddingLeft: 20,
            marginTop: 10,
            marginBottom: 20,
            '& p': {
              fontSize: 12,
              lineHeight: '30px'
            }
          }}
        >
          <Box display={'flex'}>
            <Typography color={theme.textColor.text5}>{t('market.estimatePrice')}</Typography>
            <Typography ml={10} fontWeight={'bold'}>
              {charisma.value.toString() &&
              vitality.value.toString() &&
              intellect.value.toString() &&
              dexterity.value.toString()
                ? Number(
                    Number(
                      charisma.value + vitality.value * 0.05 + intellect.value * 0.05 + dexterity.value * 0.05
                    ).toFixed(4)
                  ).toString()
                : '--'}
              U
            </Typography>
          </Box>
          <Box display={'flex'}>
            <Typography ml={isSmDown ? 0 : 40} color={theme.textColor.text5}>
              {t('market.finalPrice')}
            </Typography>
            <Typography ml={10} fontWeight={'bold'}>
              {nft?.lastTradePrice ? TokenAmount.ether(nft.lastTradePrice).toFixed(2, { groupSeparator: ',' }) : '--'} U
            </Typography>
          </Box>
        </Stack>

        {pageModel === PageModel.DEFAULT && (
          <Box>
            {isMine && listed && (
              <Stack>
                <Stack direction={'row'} alignItems={'center'} spacing={8}>
                  <Typography fontFamily={'swiss!important'} sx={{ fontWeight: 900, fontSize: 20 }}>
                    {salePrice?.toFixed(2, { groupSeparator: ',' })}
                  </Typography>
                  {saleToken && <AssetLogo address={saleToken} />}
                </Stack>
                <Box sx={{ marginTop: 20, width: { xs: '100%', sm: 180 }, height: { xs: 50, sm: 40 } }}>
                  <ActionButton
                    style={{ height: '100%', width: '100%' }}
                    pending={canceling}
                    pendingText={t('canceling')}
                    onAction={cancelCallback}
                    actionText={t('unList')}
                  />
                </Box>
              </Stack>
            )}

            {isMine && staked && (
              <Stack direction={'row'} spacing={20} style={{ marginTop: 50 }} sx={{ width: { xs: '100%', sm: 340 } }}>
                <Stack sx={{ alignItems: 'center', width: '100%' }} spacing={4}>
                  <Typography fontFamily={'swiss!important'} sx={{ fontWeight: 900, fontSize: { xs: 12, sm: 20 } }}>
                    {duration?.toString() && lastUpdateTime?.toString()
                      ? toDeltaTimer(Number(duration) + nowDate / 1000 - Number(lastUpdateTime))
                      : '--'}
                  </Typography>
                  <Typography color={'#8A899E'} fontSize={'12px'}>
                    {t('stakeTime')}
                  </Typography>
                  <Box style={{ marginTop: 20 }} sx={{ width: { xs: '100%', sm: 180 }, height: { xs: 50, sm: 40 } }}>
                    <ActionButton
                      style={{ height: '100%', width: '100%' }}
                      pending={withdrawing}
                      pendingText={t('withdrawing')}
                      onAction={withdrawCallback}
                      actionText={t('withdraw')}
                    />
                  </Box>
                </Stack>
                <Stack sx={{ alignItems: 'center', width: '100%' }} spacing={4}>
                  <Typography fontFamily={'swiss!important'} sx={{ fontWeight: 900, fontSize: { xs: 12, sm: 20 } }}>
                    {reward ? reward.toFixed(2, { groupSeparator: ',' }) : '--'}
                  </Typography>
                  <Typography sx={{ color: '#8A899E', fontSize: 12, display: 'flex', alignItems: 'center' }}>
                    {t('apReward')}
                    <BaseTooltip title={t('market.claimAPtooltip')} placement="top">
                      <HelpOutlineOutlinedIcon sx={{ height: 14, width: 14, marginLeft: 6 }} />
                    </BaseTooltip>
                  </Typography>
                  <Box style={{ marginTop: 20 }} sx={{ width: { xs: '100%', sm: 180 }, height: { xs: 50, sm: 40 } }}>
                    <ActionButton
                      style={{
                        height: '100%',
                        width: '100%',
                        background: 'white',
                        border: '1px solid #C8C8D4',
                        color: '#141420'
                      }}
                      onAction={() => {
                        if (!reward || reward.equalTo('0')) {
                          showModal(<TipModal tip={t('noReward')} />)
                          return
                        }
                        showModal(<ClaimModal tokenId={params.id} />)
                      }}
                      actionText={`${t('claim')} AP`}
                    />
                  </Box>
                </Stack>
              </Stack>
            )}
            {!staked && !listed && isMine && showBtn && (
              <Stack direction={'row'} spacing={20} sx={{ height: { xs: 50, sm: 40 }, width: { xs: '100%', sm: 380 } }}>
                <ActionButton
                  disableAction={staking}
                  pending={selling}
                  pendingText={t('selling')}
                  style={{ height: '100%', width: '100%' }}
                  onAction={() => {
                    if (!level || JSBI.lessThan(JSBI.BigInt(level), JSBI.BigInt(2))) {
                      showModal(<TipModal tip={t('mustHigherThanLevel2')} />)
                      return
                    }
                    setOpenMakeModal(true)
                  }}
                  actionText={t('sale')}
                />
                <ActionButton
                  disableAction={selling}
                  pending={approvalState === ApprovalState.PENDING || staking}
                  pendingText={staking ? t('staking') : t('approving')}
                  style={{
                    height: '100%',
                    width: '100%',
                    background: 'white',
                    border: '1px solid #C8C8D4',
                    color: '#141420'
                  }}
                  onAction={() => {
                    approvalState === ApprovalState.NOT_APPROVED ? approveCallback() : stakeCallback()
                  }}
                  actionText={t('deposit')}
                />
              </Stack>
            )}
          </Box>
        )}

        {pageModel === PageModel.ORDER && (
          <Box>
            <Typography mt={60} color={theme.textColor.text5}>
              {t('market.price')}
            </Typography>
            <Stack mb={20} direction={'row'} alignItems={'center'} spacing={6}>
              <Typography fontWeight={'bold'} fontSize={36}>
                {priceAmount ? priceAmount.toFixed(2, { groupSeparator: ',' }) : '--'}
              </Typography>
              <Image width={'24px'} src={USDT_IMG} />
            </Stack>
            <ActionButton
              style={{ height: '40px', width: '180px' }}
              onAction={() => {
                priceAmount && setOpenTakeModal(true)
              }}
              actionText={t('market.buy')}
            />
          </Box>
        )}
      </Box>
    )
  }, [
    approvalState,
    approveCallback,
    cancelCallback,
    canceling,
    charisma.value,
    dexterity.value,
    duration,
    intellect.value,
    isMine,
    isSmDown,
    lastUpdateTime,
    level,
    listed,
    nft?.lastTradePrice,
    pageModel,
    params.id,
    priceAmount,
    reward,
    salePrice,
    saleToken,
    selling,
    showModal,
    stakeCallback,
    staked,
    staking,
    t,
    theme.textColor.text5,
    vitality.value,
    withdrawCallback,
    withdrawing
  ])
  return (
    <Box
      sx={{
        flexDirection: 'column',
        display: 'flex',
        padding: { xs: '15px', sm: '40px 200px' },
        minWidth: { xs: '100%', sm: '1200px' },
        maxWidth: { xs: '1350px' },
        margin: 'auto'
      }}
    >
      <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={isCopied} autoHideDuration={6000}>
        <Alert severity="success" sx={{ width: '100%' }}>
          Link Copied!
        </Alert>
      </Snackbar>

      {openUpModal && (
        <UpgradeModal
          isOpen={openUpModal}
          onDismiss={() => setOpenUpModal(false)}
          max={upData.max}
          tokens={upData.tokens}
          tokenName={nft?.name || ''}
          tokenId={upData.tokenId}
          attr={upData.attr}
          title={upData.title}
          levelMax={upData.levelMax}
          value={upData.value}
        />
      )}

      <TakeOrderModal
        isOpen={openTakeModal}
        onDismiss={() => setOpenTakeModal(false)}
        id={params.id}
        name={nft?.name ?? ''}
        level={nft?.level ?? '0'}
        price={priceAmount}
      />

      <MakeOrderModal
        isOpen={openMakeModal}
        onDismiss={() => setOpenMakeModal(false)}
        estimatePrice={
          charisma.value.toString() &&
          vitality.value.toString() &&
          intellect.value.toString() &&
          dexterity.value.toString()
            ? Number(
                Number(
                  charisma.value +
                    Number(vitality.value) * 0.05 +
                    Number(intellect.value) * 0.05 +
                    Number(dexterity.value) * 0.05
                ).toFixed(4)
              ).toString()
            : '--'
        }
        id={params.id}
        name={nft?.name ?? '--'}
      />

      <BuyPropsModal isOpen={openBuyPropsModal} onDismiss={() => setOpenBuyPropsModal(false)} />

      <Box mb={15}>
        <Image
          onClick={() => history.goBack()}
          src={ArrowLeft}
          width={27}
          style={{
            cursor: 'pointer'
          }}
        />
      </Box>
      <Stack width={'100%'} direction={isSmDown ? 'column-reverse' : 'row'}>
        {isSmDown && operation}
        <Stack spacing={18} flex={3} sx={{ margin: { xs: '10px 0', sm: 'unset' } }}>
          <Image
            onClick={() => showModal(<CoverModal overflow={'hidden'} url={`${BASE_IMAGE_URL}${params.id}.png`} />)}
            style={{ borderRadius: '6px', width: '100%' }}
            src={`${BASE_IMAGE_URL}${params.id}.png`}
          />
        </Stack>
        <Stack ml={isSmDown ? 0 : 60} flex={4}>
          <Stack direction={'row'} spacing={20}>
            <Typography fontSize={14} color={theme.textColor.text5}>
              {t('market.holder')}
            </Typography>
            <ExternalLink href={nft?.owner ? getEtherscanLink(ChainId.MAINNET, nft.owner, 'address') : ''}>
              <Typography fontSize={12} color={theme.textColor.primary}>
                {nft?.owner ? shortenAddress(nft.owner) : '--'}
              </Typography>
            </ExternalLink>
            <Stack direction={'row'} style={{ marginLeft: 'auto', marginRight: 0 }}>
              <Box
                onClick={() => setCopied(`https://${window.location.hostname}/#/bird/detail/${params.id}`)}
                sx={{ width: 24, height: 24, marginRight: 10, marginLeft: 'auto', cursor: 'pointer' }}
              >
                <Image style={{ width: '100%', height: '100%' }} src={share} />
              </Box>
              <Box sx={{ width: 20, height: 20, marginRight: 0 }}>
                <ExternalLink href={`${BASE_IMAGE_URL}${params.id}.png`}>
                  <Image style={{ width: '100%', height: '100%' }} src={enlarge} />
                </ExternalLink>
              </Box>
            </Stack>
          </Stack>
          <Typography
            sx={{ fontSize: { xs: 28, sm: 36 } }}
            fontFamily={{ fontFamily: 'Arial Black!important' }}
            mt={10}
            fontWeight={'bold'}
            color={'black'}
          >
            {nft?.name ? nft.name : '--'}
          </Typography>
          <Stack mt={isSmDown ? 10 : 20} direction={isSmDown ? 'column' : 'row'} spacing={isSmDown ? 0 : 20} flex={1}>
            <Typography
              fontSize={12}
              textAlign={isSmDown ? 'left' : 'center'}
              border={isSmDown ? 'none' : '1px solid #F1F0F2'}
              height={isSmDown ? 'auto' : 30}
              color={theme.textColor.text5}
              borderRadius={'15px'}
              lineHeight={'30px'}
              width={260}
            >
              {t('market.mintTime')} &nbsp;&nbsp;{' '}
              {nft?.createdTime ? moment(Number(nft.createdTime) * 1000).format('YYYY/MM/DD HH:mm:ss') : '--'}
            </Typography>
            <Stack
              sx={{
                flexDirection: 'row',
                width: 200,
                border: isSmDown ? 'none' : '1px solid #F1F0F2',
                borderRadius: '15px',
                justifyContent: isSmDown ? 'unset' : 'center',
                height: 30,
                '& p': {
                  fontSize: 12,
                  lineHeight: '30px'
                }
              }}
            >
              <Typography color={theme.textColor.text5}>{t('market.mintAddress')} &nbsp;&nbsp;</Typography>
              <ExternalLink href={nft?.creator ? getEtherscanLink(ChainId.MAINNET, nft.creator, 'address') : ''}>
                <Typography color={theme.textColor.primary}>{nft?.creator && shortenAddress(nft.creator)}</Typography>
              </ExternalLink>
            </Stack>
          </Stack>
          {!isSmDown && operation}
        </Stack>
      </Stack>
      <Stack width={'100%'} mb={40} direction={isSmDown ? 'column-reverse' : 'row'}>
        <Stack flex={3} spacing={20}>
          <Typography fontSize={20} mt={isSmDown ? 40 : 60} fontWeight={'bold'}>
            {t('market.detail')}
          </Typography>
          <Divider />
          <InfoItem
            isLink={LYNKNFT_ADDRESS}
            title={t('market.contractAddress')}
            content={shortenAddress(LYNKNFT_ADDRESS).toString()}
          />
          <InfoItem title={t('market.tokenId')} content={nft?.id ?? ''} />
          <InfoItem title={t('market.standard')} content={'ERC-721'} />
          <InfoItem title={t('market.chain')} content={t(`chain.${globalonfig.chainName}`)} />
          {/* <InfoItem title={t('market.grade')} content={getGrade(Number(tokenId))} /> */}
          {/* <InfoItem title={t('market.type')} content={getFaction(Number(tokenId))} /> */}
          {recessiveAttr.map((item, index) => {
            return <InfoItem key={index} title={item?.trait_type} content={item?.value} />
          })}
        </Stack>
        {showBtn ? (
          <Stack spacing={20} flex={4} ml={isSmDown ? 0 : 60} sx={{ '& p': { fontWeight: 'bold' } }}>
            <Stack mt={isSmDown ? 40 : 60} direction={'row'} alignItems={'center'} justifyContent={'space-between'}>
              <Typography fontSize={20} fontWeight={'bold'}>
                {t('market.attr')}
              </Typography>
              {!staked && !listed && isMine && (
                <BlackButton onClick={() => setOpenBuyPropsModal(true)}>{t('buyProps')}</BlackButton>
              )}
            </Stack>
            <Divider color={theme.textColor.text5} />
            <Stack direction={'row'} alignItems="center">
              <Typography>LV {level ? level.toString() : '--'}/13</Typography>
              <BaseTooltip title={t('market.leveltooltip')} placement="top">
                <HelpOutlineOutlinedIcon sx={{ height: 14, width: 14, marginLeft: 6 }} />
              </BaseTooltip>
            </Stack>
            <Progress
              upgradeBle={upgradeBle}
              onUpgrade={() => upgradeCallback(Attr.CA)}
              icon={caIcon}
              title={'charisma'}
              current={charisma.value}
              max={charisma.max}
              levelMax={charisma.levelMax}
              upgrading={charismaUpgrade}
            />
            <Progress
              upgradeBle={upgradeBle}
              onUpgrade={() => upgradeCallback(Attr.VA)}
              icon={vaIcon}
              title={'vitality'}
              current={vitality.value}
              max={vitality.max}
              levelMax={vitality.levelMax}
              upgrading={vitalityUpgrade}
            />
            <Progress
              upgradeBle={upgradeBle}
              onUpgrade={() => upgradeCallback(Attr.IN)}
              icon={inIcon}
              title={'intellect'}
              current={intellect.value}
              max={intellect.max}
              levelMax={intellect.levelMax}
              upgrading={intellectUpgrade}
            />
            <Progress
              upgradeBle={upgradeBle}
              onUpgrade={() => upgradeCallback(Attr.DX)}
              icon={dxIcon}
              title={'dexterity'}
              current={dexterity.value}
              max={dexterity.max}
              levelMax={dexterity.levelMax}
              upgrading={dexterityUpgrade}
            />
          </Stack>
        ) : (
          <Stack spacing={20} flex={4} ml={isSmDown ? 0 : 60} sx={{ '& p': { fontWeight: 'bold' } }}></Stack>
        )}
      </Stack>
    </Box>
  )
}
