import { ArrowBack, ArrowDropDown, ArrowDropUp, ArrowForward, Search } from '@mui/icons-material'
import { Backdrop, Box, CircularProgress, Link, MenuItem, styled, Typography } from '@mui/material'
import Input from 'components/Input'
import Select from 'components/Select/Select'
import Table from 'components/Table'
import useBreakpoint from 'hooks/useBreakpoint'
import { useMemo, useState, useEffect } from 'react'
import { getEtherscanLink, shortenAddress } from 'utils'
import rankingOne from 'assets/images/ranking_one.png'
import rankingTwo from 'assets/images/ranking_two.png'
import rankingThree from 'assets/images/ranking_three.png'
// import defaultNftImg from 'assets/images/default_nft.png'
import shengshenImg from 'assets/images/shengshen_en.png'
import chuanqiImg from 'assets/images/chuanqi_en.png'
import shenghuaImg from 'assets/images/shenghua_en.png'
import dashiImg from 'assets/images/dashi_en.png'
import shishiImg from 'assets/images/shishi_en.png'
import jinyingImg from 'assets/images/jinying_en.png'
import Image from 'components/Image'
import { useRankingList } from 'hooks/useRanking'
import { NFT } from 'utils/fetch/graph'
import { ChainId } from 'constants/chain'
import { BASE_IMAGE_URL } from '../../constants'
import { useI18n } from 'react-simple-i18n'

const StyledWrapper = styled(Box)(({}) => ({
  width: '100%',
  maxWidth: 1420,
  margin: 'auto',
  padding: '0 20px'
}))

const Text = styled(Typography)({
  fontWeight: 'bold'
})

type Sort = 'asc' | 'desc'
type RankingTypeValue = 'ranking' | 'level' | 'charisma'
const rankingType = ['Top Ranking', 'Top Level', 'Top Charisma']
const rankingTypeValue: RankingTypeValue[] = ['ranking', 'level', 'charisma']

export default function Ranking() {
  const { t } = useI18n()
  const isMdDown = useBreakpoint('md')
  const [curRankingType, setCurRankingType] = useState(1)
  const [searchContent, setSearchContent] = useState('')
  const [sort, setSort] = useState<Sort>('desc')
  const { result: rankingList, refresh, loading, page } = useRankingList(
    searchContent,
    sort,
    rankingTypeValue[curRankingType]
  )

  const showRanking = useMemo(() => page.currentPage === 1 && !searchContent, [page.currentPage, searchContent])

  const headers = useMemo(() => {
    switch (curRankingType) {
      case 0:
        return ['Ranking', 'Level', 'TX']
      case 1:
        return ['Ranking', 'NFT', 'Level', 'TX']
      default:
        return ['Ranking', 'NFT', 'CA', 'TX']
    }
  }, [curRankingType])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const showList = useMemo(() => {
    if (curRankingType === 0) {
      return rankingList?.map((i, index) => {
        const item = i as {
          address: string
          id: string
          level: number
        }
        return (
          [
            <Box key={0} display="flex" alignItems={'center'}>
              <Text mr={10} fontSize={showRanking ? 24 : 24}>
                {(page.currentPage - 1) * page.pageSize + index + 1}
              </Text>
              {showRanking && index <= 2 && (
                <Image width={28} src={index === 0 ? rankingOne : index === 1 ? rankingTwo : rankingThree} />
              )}
            </Box>,
            <Box style={{ justifyContent: 'start' }} key={1} display="flex" alignItems={'center'}>
              <Image
                style={{ width: showRanking ? 70 : 70 }}
                src={
                  item.level === 5
                    ? shengshenImg
                    : item.level === 4
                    ? shenghuaImg
                    : item.level === 3
                    ? chuanqiImg
                    : item.level === 2
                    ? dashiImg
                    : item.level === 1
                    ? shishiImg
                    : jinyingImg
                }
              />
            </Box>,
            <Link target={'_blank'} href={getEtherscanLink(ChainId.MAINNET, item.address, 'address')} key={6}>
              <Text key={3} textAlign="right" fontSize={showRanking ? 16 : 16}>
                {item?.address ? shortenAddress(item.address) : '-'}
              </Text>
            </Link>
          ] || []
        )
      })
    } else {
      return rankingList?.map((i, index) => {
        const item = i as NFT
        return (
          [
            <Box key={0} display="flex" alignItems={'center'}>
              <Text mr={10} fontSize={showRanking ? 24 : 24}>
                {(page.currentPage - 1) * page.pageSize + index + 1}
              </Text>
              {showRanking && index <= 2 && (
                <Image width={28} src={index === 0 ? rankingOne : index === 1 ? rankingTwo : rankingThree} />
              )}
            </Box>,
            <Box pl={'30%'} key={1} display={isMdDown ? 'grid' : 'flex'} alignItems={'center'} justifyItems="end">
              <Image width={showRanking ? 70 : 70} src={`${BASE_IMAGE_URL}${item.id}.png`} />
              <Text ml={10} fontSize={showRanking ? 12 : 12}>
                {item.name}
              </Text>
            </Box>,
            <Text key={2} textAlign="center" fontSize={showRanking ? 16 : 16}>
              {curRankingType === 2 ? item.charisma : `LV${item.level}`}
            </Text>,
            <Link href={`/#/bird/detail/${item.id}`} key={6}>
              <Text key={3} textAlign="right" fontSize={showRanking ? 16 : 16}>
                {item.owner ? shortenAddress(item.owner) : '-'}
              </Text>
            </Link>
          ] || []
        )
      })
    }
  }, [curRankingType, isMdDown, page.currentPage, page.pageSize, rankingList, showRanking])

  return (
    <Box margin="40px 0">
      <StyledWrapper>
        <Box
          display={'flex'}
          pb={15}
          borderBottom="1px solid #F1F0F2"
          alignItems="center"
          justifyContent={'space-between'}
        >
          <Typography fontSize={18} fontWeight="bold">
            {t('rankTitle')}
          </Typography>
          <Box display="flex" alignItems={'center'}>
            {!isMdDown && (
              <SearchBox
                sort={sort}
                refresh={refresh}
                placeholder={curRankingType === 0 ? 'Enter address' : 'Enter name'}
                toggleSort={() => setSort(sort === 'asc' ? 'desc' : 'asc')}
                searchContent={searchContent}
                onSearchContent={val => setSearchContent(val)}
              />
            )}

            <Select
              width={160}
              height={34}
              style={{ marginLeft: 20 }}
              placeholder=""
              value={curRankingType}
              onChange={e => setCurRankingType(e.target.value)}
            >
              {rankingType.map((item, index) => (
                <MenuItem key={index} value={index} selected={index === curRankingType}>
                  {item}
                </MenuItem>
              ))}
            </Select>
          </Box>
        </Box>

        {isMdDown && (
          <SearchBox
            sort={sort}
            placeholder={curRankingType === 0 ? 'Enter address' : 'Enter name'}
            refresh={refresh}
            toggleSort={() => setSort(sort === 'asc' ? 'desc' : 'asc')}
            searchContent={searchContent}
            onSearchContent={val => setSearchContent(val)}
          />
        )}

        <Backdrop sx={{ color: '#fff', zIndex: theme => theme.zIndex.drawer + 1 }} open={loading}>
          <CircularProgress color="inherit" />
        </Backdrop>

        <Table isRanking showRanking={showRanking} header={headers} rows={showList || []} />
        <Box mt={20} display="flex" justifyContent="end">
          <Box
            display={'flex'}
            alignItems="center"
            sx={{
              '&>*': {
                ml: '10px !important',
                cursor: 'pointer'
              },
              '& .disabled': {
                opacity: 0.5
              }
            }}
          >
            <ArrowBack
              onClick={() => page.hasPrev && page.setCurrentPage(page.currentPage - 1)}
              className={page.hasPrev ? '' : 'disabled'}
            />
            <Typography fontSize={20}>{page.currentPage}</Typography>
            <ArrowForward
              onClick={() => page.hasNext && page.setCurrentPage(page.currentPage + 1)}
              className={page.hasNext ? '' : 'disabled'}
            />
          </Box>
        </Box>
      </StyledWrapper>
    </Box>
  )
}

function SearchBox({
  searchContent,
  onSearchContent,
  refresh,
  sort,
  placeholder,
  toggleSort
}: {
  searchContent: string
  onSearchContent: (val: string) => void
  sort: Sort
  toggleSort: () => void
  refresh: () => void
  placeholder?: string
}) {
  const [curInput, setCurInput] = useState(searchContent)
  return (
    <Box
      sx={{
        display: 'grid',
        // gridTemplateColumns: { md: '250px 120px', xs: '1fr 120px' },
        alignItems: 'center',
        gap: 20,
        margin: { xs: '10px 0', md: 0 }
      }}
    >
      <Input
        height={34}
        placeholder={placeholder}
        style={{ padding: '0 15px' }}
        value={curInput}
        onChange={e => setCurInput(e.target.value)}
        startAdornment={<Search sx={{ mr: 5 }} />}
        onBlur={e => {
          onSearchContent(e.target.value)
        }}
        endAdornment={
          <Typography
            noWrap
            onClick={refresh}
            sx={{
              cursor: 'pointer',
              paddingLeft: 10,
              borderLeft: '1px solid #C5C4D6'
            }}
          >
            Search
          </Typography>
        }
      />
      <Box
        onClick={toggleSort}
        sx={{
          cursor: 'pointer',
          padding: '6px 15px',
          height: 34,
          border: '1px solid #C5C4D6',
          borderRadius: '23px',
          // display: 'flex',
          display: 'none',
          justifyContent: 'space-between',
          alignItems: 'center'
        }}
      >
        <Typography>排序</Typography>
        <Box
          sx={{
            height: 24
          }}
        >
          {sort == 'asc' ? <ArrowDropUp /> : <ArrowDropDown />}
        </Box>
      </Box>
    </Box>
  )
}
