import { Grid, Stack, Typography } from '@mui/material'

export function FactionsCard({
  factions,
  color,
  active,
  onClick
}: {
  factions: string
  color: string
  active: boolean
  onClick: () => void
}) {
  return (
    <Grid
      onClick={onClick}
      item
      xs={6}
      sx={{
        padding: 6,
        cursor: 'pointer'
      }}
    >
      <Stack
        spacing={8}
        sx={{
          border: `1px solid ${color}`,
          height: 50,
          borderRadius: 100,
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: active ? color : 'white',
          '&: hover': {
            boxShadow: `0px 0px 3px ${color}`
          }
        }}
      >
        <Typography style={{ color: active ? 'white' : 'black' }} textAlign={'center'}>
          {factions}
        </Typography>
        {/*<Typography sx={{ fontSize: '12px!important', color: '#8A899E' }}>*/}
        {/*  {t('price')} {price}U/LYNK*/}
        {/*</Typography>*/}
      </Stack>
    </Grid>
  )
}
