import { BlueButton } from './Button'
import OutlineButton from './OutlineButton'
import Spinner from 'components/Spinner'
import { Theme, Typography } from '@mui/material'
import React from 'react'
import { SxProps } from '@mui/system'
import { useActiveWeb3React } from '../../hooks'
import { useWalletModalToggle } from '../../state/application/hooks'

export default function ActionButton({
  style,
  error,
  pending,
  success,
  onAction,
  actionText,
  pendingText,
  disableAction,
  successText
}: {
  style?: React.CSSProperties & SxProps<Theme>
  error?: string | undefined
  pending?: boolean
  success?: boolean
  onAction: (() => void) | undefined
  actionText: string
  pendingText?: string
  successText?: string
  height?: string
  width?: string
  disableAction?: boolean
}) {
  const { account } = useActiveWeb3React()
  const toggleWalletModal = useWalletModalToggle()

  return (
    <>
      {error || pending ? (
        <OutlineButton style={{ fontSize: 14 }} primary disabled width={style?.width} height={style?.height}>
          {pending ? (
            <>
              <Spinner marginRight={16} />
              {pendingText || 'Waiting Confirmation'}
            </>
          ) : (
            error
          )}
        </OutlineButton>
      ) : success ? (
        <BlueButton disabled style={style}>
          <Typography variant="inherit">{successText ?? actionText}</Typography>
        </BlueButton>
      ) : (
        <BlueButton
          style={style}
          onClick={e => {
            e?.stopPropagation()
            if (!account) {
              toggleWalletModal()
            } else {
              onAction && onAction()
            }
          }}
          disabled={disableAction}
        >
          {actionText}
        </BlueButton>
      )}
    </>
  )
}
