import React from 'react'
import Modal from '../index'
import { Box, keyframes, Stack, styled, Typography, useTheme } from '@mui/material'
import Image from '../../Image'
import USDT_IMG from '../../../assets/images/usdt.png'
import checked from '../../../assets/images/checked.png'
import egg from '../../../assets/images/egg.png'
import eggLoading from '../../../assets/images/egg_loading.png'
import { ReactComponent as BaseLoading } from '../../../assets/images/loading.svg'
import { useI18n } from 'react-simple-i18n'
import { BASE_IMAGE_URL, USDT } from '../../../constants'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { useActiveWeb3React } from '../../../hooks'

const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`

const Loading = styled(BaseLoading)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  animation: ${rotate} 2s linear infinite;
  transform: translate3d(0, 0, 0);
`

const Circle = styled(Box)(({ active, disabled }: { active?: boolean; disabled?: boolean }) => ({
  width: 16,
  height: 16,
  borderRadius: 8,
  backgroundColor: disabled ? '#ffffff' : '#0167BC',
  border: disabled ? '1px solid #8A899E' : 'none',
  backgroundImage: active ? `url('${checked}')` : 'none',
  backgroundSize: '100%'
}))

const Line = styled(Box)({
  flex: 1,
  height: 2,
  backgroundColor: '#0167BC'
})

export default function MintModal({
  isOpen,
  children,
  customOnDismiss,
  nextId,
  approved,
  submitted,
  finish
}: {
  isOpen: boolean
  children?: React.ReactNode
  customOnDismiss: () => void
  nextId: string
  approved: boolean
  submitted: boolean
  finish: boolean
}) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const theme = useTheme()
  const balance = useTokenBalance(account ?? undefined, USDT)

  return (
    <Modal
      width={'320px'}
      maxWidth={'320px'}
      isCardOnMobile
      title={t('fledgling.mintNFT')}
      customIsOpen={isOpen}
      closeIcon
      customOnDismiss={customOnDismiss}
    >
      <Stack sx={{ margin: 20, flexDirection: 'column', marginTop: { xs: 30 } }} alignItems={'center'}>
        <Stack spacing={4} direction={'row'} width={'100%'}>
          <Stack spacing={6} alignItems={'center'} justifyContent={'center'}>
            <Circle active={approved} />
            <Typography fontSize={12}>{t('approve')}</Typography>
          </Stack>
          <Line style={{ marginTop: 8 }} />
          <Stack spacing={6} alignItems={'center'} justifyContent={'center'}>
            <Circle active={submitted || finish} disabled={!approved} />
            <Typography fontSize={12}>{t('mint')}</Typography>
          </Stack>
          <Line style={{ marginTop: 8 }} />
          <Stack spacing={6} alignItems={'center'} justifyContent={'center'}>
            <Circle active={finish} disabled={!submitted} />
            <Typography fontSize={12}>{t('finish')}</Typography>
          </Stack>
        </Stack>
        <Stack spacing={10} alignItems={'center'} mt={20}>
          <Box
            position={'relative'}
            sx={{ width: { xs: '80%', sm: theme.width.smallCard }, height: { xs: '80%', sm: theme.width.smallCard } }}
          >
            <Image
              style={{
                borderRadius: '6px',
                width: '100%',
                height: '100%',
                overflow: 'hidden'
              }}
              width={'240px'}
              height={'240px'}
              src={nextId ? `${BASE_IMAGE_URL}${nextId}.png` : submitted ? eggLoading : egg}
            />
            {submitted && <Loading />}
          </Box>
          <Stack
            sx={{ justifyContent: nextId ? 'space-between' : 'center', width: '100%' }}
            direction={'row'}
            spacing={4}
          >
            {nextId && (
              <Typography fontSize={16} fontFamily={'swiss!important'} fontWeight={'900'}>
                {nextId}.Lynk
              </Typography>
            )}
            <Box display={'flex'}>
              <Typography fontSize={16} marginRight={10} fontFamily={'swiss!important'} fontWeight={'900'}>
                {nextId ? 'LV 0' : 'Price 5000'}
              </Typography>
              {!nextId && <Image style={{ objectFit: 'contain' }} width={'16px'} src={USDT_IMG} />}
            </Box>
          </Stack>
        </Stack>
        {!nextId && (
          <Typography textAlign={'center'} fontSize={14} width={'100%'} marginTop={20}>
            <span style={{ color: '#8A899E' }}>{t('balance')}</span>:{' '}
            {balance ? balance?.toFixed(2, { groupSeparator: ',' }) : '--'}
          </Typography>
        )}

        <Stack mt={10} height={'fit-content'} width={'100%'} spacing={30}>
          {children}
        </Stack>
      </Stack>
    </Modal>
  )
}
