import { FormControlLabel, Radio } from '@mui/material'

interface Props {
  checked: boolean
  onChange?: () => void
  label?: string
  disabled?: boolean
}

export default function _Radio(props: Props) {
  const { checked, onChange, label = '', disabled } = props
  return (
    <FormControlLabel
      sx={{
        color: '#141420',
        fontSize: 14,
        marginRight: { xs: 15, md: 30 },
        '& .MuiTypography-root': {
          color: '#141420',
          fontSize: 14
        }
      }}
      control={
        <Radio
          sx={{
            padding: 0,
            marginLeft: 10,
            color: '#C5C4D6',
            '&.Mui-checked': {
              color: '#614DF6'
            }
          }}
        />
      }
      label={label}
      labelPlacement="start"
      onChange={onChange}
      checked={checked}
      disabled={disabled}
    />
  )
}
