import Modal from '../index'
import { Box, Stack, Typography } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import { ModalCover } from '../base/NFT'
import { TokenAmount } from '../../../constants/token'
import ActionButton from '../../Button/ActionButton'
import { useCallback } from 'react'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import { ApprovalState, useApproveCallback } from '../../../hooks/useApproveCallback'
import { MARKET_ADDRESS } from '../../../constants'
import useModal from '../../../hooks/useModal'
import { useMarket } from '../../../hooks/useMarket'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { useActiveWeb3React } from '../../../hooks'

export default function TakeOrderModal({
  id,
  name,
  level,
  price,
  isOpen,
  onDismiss
}: {
  id: string
  name: string
  level: string
  price: TokenAmount | undefined
  isOpen: boolean
  onDismiss: () => void
}) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const { showModal, hideModal } = useModal()
  const { take } = useMarket()

  const [approvalState, approveCallback] = useApproveCallback(price, MARKET_ADDRESS)

  const balance = useTokenBalance(account ?? undefined, price?.token)

  const enoughAsset = balance && price && price.lessThan(balance)

  const takeCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    take(id)
      .then(() => {
        hideModal()
        onDismiss()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, take, id, hideModal, onDismiss])

  return (
    <Modal isCardOnMobile title={`${t('buy')}`} closeIcon customOnDismiss={onDismiss} customIsOpen={isOpen}>
      <Stack
        direction={'row'}
        sx={{ margin: 20, flexDirection: { xs: 'column', sm: 'row' }, marginTop: { xs: 30 } }}
        alignItems={'flex-start'}
      >
        <Stack sx={{ marginRight: { xs: 0, sm: 20 } }} spacing={10} alignItems={'center'} width={'100%'}>
          <ModalCover id={id} />
          <Stack
            direction={'row'}
            justifyContent={'space-between'}
            sx={{
              width: '100%',
              ' p': {
                fontSize: 18,
                fontWeight: 900,
                margin: '0 10px'
              }
            }}
          >
            <Typography fontFamily={'swiss!important'} fontWeight={'900'}>
              {name}
            </Typography>
            <Typography fontFamily={'swiss!important'} fontWeight={'900'}>
              LV {level}
            </Typography>
          </Stack>
        </Stack>
        <Stack alignItems={'center'} sx={{ height: { xs: 'fit-content', sm: 200 } }} width={'100%'} spacing={10}>
          <Stack sx={{ margin: { xs: '30px auto', sm: 'auto' } }} direction={'row'} spacing={4} width={'fit-content'}>
            <Typography alignSelf={'end'} fontSize={12} color={'#8A899E'}>
              {t('price')}
            </Typography>
            <Typography lineHeight={'10px'} fontSize={24} fontFamily={'swiss!important'} fontWeight={'bold'}>
              {price ? price?.toFixed(2, { groupSeparator: ',' }) : '--'}
            </Typography>
            <Typography alignSelf={'end'}>{price?.token?.symbol}</Typography>
          </Stack>
          <Typography>
            {t('balance')}: {balance ? balance?.toFixed(2, { groupSeparator: ',' }) : '--'}
          </Typography>
          <Box width={'100%'}>
            <ActionButton
              disableAction={!enoughAsset}
              pending={approvalState === ApprovalState.PENDING}
              pendingText={t('approving')}
              style={{ width: '100%', background: 'black', height: 50 }}
              onAction={approvalState === ApprovalState.NOT_APPROVED ? approveCallback : takeCallback}
              actionText={
                !enoughAsset
                  ? t('insufficient')
                  : approvalState === ApprovalState.NOT_APPROVED
                  ? t('approve')
                  : t('confirm')
              }
            />
          </Box>
        </Stack>
      </Stack>
    </Modal>
  )
}
