import { Box, Grid, Paper, Skeleton, Theme, Typography, useTheme } from '@mui/material'
import { SxProps } from '@mui/system'
import React, { useEffect, useState } from 'react'
import { BaseNFT, NFT, Order } from '../../utils/fetch/graph'
import Image from '../Image'
import { BASE_IMAGE_URL } from '../../constants'
import { BetweenRow } from '../row'
import defaultImg from '../../assets/images/newDefault.png'
import Tooltip from '../essential/QuestionHelper/Tooltip'

export default function Card({
  children,
  color,
  padding,
  width,
  style,
  gray,
  primary
}: {
  children?: React.ReactNode
  color?: string
  padding?: string | number
  width?: string | number
  style?: React.CSSProperties & SxProps<Theme>
  gray?: boolean
  primary?: boolean
}) {
  return (
    <Paper
      sx={{
        background: theme =>
          primary ? theme.gradient.gradient1 : gray ? theme.palette.background.default : color ?? '#ffffff',
        borderRadius: '20px',
        boxShadow: 'none',
        padding,
        width,
        ...style
      }}
    >
      {children}
    </Paper>
  )
}

export function OutlinedCard({
  children,
  color,
  padding,
  width,
  style
}: {
  children: JSX.Element | React.ReactNode
  color?: string
  padding?: string | number
  width?: string | number
  style?: React.CSSProperties & SxProps<Theme>
}) {
  return (
    <Paper
      variant="outlined"
      sx={{
        backgroundColor: 'transparent',
        border: `1px solid ${color ?? 'rgba(0, 0, 0, 0.1)'}`,
        padding,
        width,
        ...style
      }}
    >
      {children}
    </Paper>
  )
}

export function NFTCard({
  showTip,
  small,
  item,
  children,
  marketMobile,
  onClick,
  selected,
  showGrade
}: {
  showTip: boolean
  small?: boolean
  marketMobile?: boolean
  item: Order | NFT | BaseNFT
  children?: React.ReactNode
  onClick: () => void
  selected?: boolean
  showGrade?: boolean
}) {
  const theme = useTheme()
  const [show, setShow] = useState<boolean>(false)
  const [hover, setHover] = useState(false)

  useEffect(() => {
    setShow(false)
  }, [showTip])

  return (
    <Grid
      onTouchMove={e => e.preventDefault()}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={onClick}
      item
      xs={marketMobile ? 6 : undefined}
    >
      <Box
        onTouchStart={e => e.stopPropagation()}
        sx={{
          border: selected ? '1px #0167BC solid' : 'none',
          width: marketMobile ? '100%' : 'fit-content',
          boxShadow: '0px 5px 30px rgba(20,20,32,0.1)',
          borderRadius: '6px',
          overflow: 'hidden',
          cursor: 'pointer'
          // margin: 10,
          // paddingBottom: 10
        }}
      >
        <Box
          sx={{
            height: small ? theme.width.smallCard : theme.width.card,
            borderRadius: '6px 6px 0 0',
            overflow: 'hidden',
            '> img': {
              objectFit: 'cover',
              overflow: 'hidden',
              transition: 'all 0.5s ease-out',
              transform: hover ? 'scale(1.2)' : 'scale(1)'
            }
          }}
        >
          <Image
            style={{
              backgroundColor: '#F1F0F2',
              width: marketMobile ? '100%' : small ? theme.width.smallCard : theme.width.card,
              height: small ? theme.width.smallCard : theme.width.card
            }}
            src={`${BASE_IMAGE_URL}${item.id}.png`}
          />
        </Box>
        <BetweenRow padding={'10px 15px 0'}>
          <Box style={{ width: '60%' }}>
            <Tooltip text={item.name} show={show}>
              <Typography
                style={{ overflow: 'hidden', textOverflow: 'ellipsis', maxWidth: '100%' }}
                onClick={e => {
                  setShow(true)
                  e.stopPropagation()
                }}
                onMouseEnter={() => {
                  setShow(true)
                }}
                onMouseLeave={() => {
                  setShow(false)
                }}
                fontFamily={'swiss!important'}
                fontWeight={'900'}
              >
                {item.name}
              </Typography>
            </Tooltip>
          </Box>
          <Typography fontFamily={'swiss!important'} fontWeight={'900'}>
            {showGrade ? item?.grade : `LV ${item.level}`}
          </Typography>
        </BetweenRow>
        {children}
      </Box>
    </Grid>
  )
}

export function SkeletonNFTCard({ small, children }: { small?: boolean; children?: React.ReactNode }) {
  const theme = useTheme()
  return (
    <Grid
      item
      sx={{
        height: 'fit-cotent',
        width: 'fit-content',
        boxShadow: '0px 3px 19px rgba(20,20,32,0.1)',
        borderRadius: '6px',
        overflow: 'hidden',
        cursor: 'pointer',
        margin: 10,
        marginTop: 20,
        marginBottom: 0,
        paddingLeft: '0!important',
        paddingTop: '0!important'
      }}
    >
      <Skeleton
        variant="rectangular"
        width={small ? theme.width.smallCard : theme.width.card}
        height={small ? theme.width.smallCard : theme.width.card}
      />
      <BetweenRow padding={10}>
        <Skeleton width={80} variant="text" />
        <Skeleton width={80} variant="text" />
      </BetweenRow>
      {children}
    </Grid>
  )
}

export function DefaultCard({
  children,
  marketMobile,
  small
}: {
  children: React.ReactNode
  marketMobile?: boolean
  small?: boolean
}) {
  const theme = useTheme()

  return (
    <Grid item xs={marketMobile ? 6 : undefined}>
      <Box
        sx={{
          boxShadow: '0px 3px 19px rgba(20,20,32,0.1)',
          display: 'flex',
          flexDirection: 'column',
          gap: '8px'
        }}
      >
        <Box
          sx={{
            width: marketMobile ? '100%' : small ? theme.width.smallCard : theme.width.card,
            height: small ? theme.width.smallCard : theme.width.card,
            borderRadius: '6px 6px 0 0',
            padding: marketMobile ? '0 50px' : '0 100px',
            // backgroundColor: '#F1F0F2',
            backgroundImage: `url(${defaultImg})`,
            backgroundSize: 'cover',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          {/* <Image style={{ height: '100%', width: '260px' }} src={defaultImg} /> */}
        </Box>
        {children}
      </Box>
    </Grid>
  )
}
