import { Box, Grid, Typography } from '@mui/material'
import { useActiveWeb3React } from 'hooks'
import useAsyncMemo from 'hooks/useAsyncMemo'
import useBreakpoint from 'hooks/useBreakpoint'
import { useCallback, useState } from 'react'
import { getStakingList, NFT } from 'utils/fetch/graph'
import Profile from '.'
import { useI18n } from 'react-simple-i18n'
import { useLYNKClaim } from '../../hooks/useStaking'
import TransactionPendingModal from '../../components/Modal/TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../../components/Modal/TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../../components/Modal/TransactionModals/MessageBox'
import useModal from '../../hooks/useModal'
import { StakingCard } from './StakingCard'
import { useBlockNumber } from '../../state/application/hooks'
import { useUserHasSubmitted } from '../../state/transactions/hooks'
import ActionButton from '../../components/Button/ActionButton'

export default function Staking() {
  const { account } = useActiveWeb3React()
  const { t } = useI18n()
  const { claim, earnedLYNK, claimableLYNK } = useLYNKClaim()
  const { showModal, hideModal } = useModal()
  const blockNumber = useBlockNumber()
  const isSmDown = useBreakpoint('sm')
  const [show, setShow] = useState(true)

  const { submitted: claiming } = useUserHasSubmitted(`${account}_claim_lynk`)

  const getMyNFT = useCallback(async () => {
    if (!account) return []
    return await getStakingList(account)
  }, [account])

  const NFTList: NFT[] = useAsyncMemo(getMyNFT, [], [blockNumber])
  const claimCallback = useCallback(async () => {
    showModal(<TransactionPendingModal title="Confirm" />)
    claim()
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal title="Confirm" />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error" title="Fail">
            {err.error && err.error.message ? err.error.message : err?.data?.message ? err?.data?.message : err.message}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, claim, hideModal])

  return (
    <Profile>
      <Box
        onTouchMove={() => setShow(!show)}
        sx={{
          display: 'grid',
          gridTemplateColumns: { sm: '259px 1fr', xs: 'unset' },
          gap: 20
        }}
      >
        <Box mt={40}>
          <Box display={'grid'} gap={isSmDown ? 20 : 40} justifyItems="center">
            <Box
              display={'grid'}
              gap={isSmDown ? 20 : 40}
              justifyItems="center"
              sx={{
                width: '100%',
                gridTemplateColumns: { xs: '1fr 1fr', sm: 'unset' }
              }}
            >
              <Box>
                <Typography textAlign={'center'} fontSize={36} fontWeight={900}>
                  {earnedLYNK ? earnedLYNK.toFixed(2, { groupSeparator: ',' }) : '--'}
                </Typography>
                <Typography fontSize={14}>{t('earnedLYNK')}</Typography>
              </Box>
              <Box>
                <Typography textAlign={'center'} fontSize={36} fontWeight={900}>
                  {claimableLYNK ? claimableLYNK.toFixed(2, { groupSeparator: ',' }) : '--'}
                </Typography>
                <Typography fontSize={14}>{t('claimableLYNK')}</Typography>
              </Box>
            </Box>
            <Box padding="0 25px" width={'100%'}>
              <ActionButton
                pending={claiming}
                pendingText={t('claiming')}
                disableAction={claimableLYNK && claimableLYNK.equalTo('0')}
                onAction={claimCallback}
                actionText={t('claim')}
                style={{
                  width: '100%',
                  height: '50px',
                  background: 'black',
                  fontSize: 18,
                  borderRadius: 25
                }}
              />
            </Box>
          </Box>
        </Box>
        <Grid container mt={10} mb={20} spacing={isSmDown ? 15 : 20}>
          {[...NFTList].map(nft => {
            return <StakingCard shopTip={show} key={nft.id} nft={nft} />
          })}
        </Grid>
      </Box>
    </Profile>
  )
}
