import Modal from '../index'
import { Grid, RadioGroup, Stack, Typography } from '@mui/material'
import Divider from '../../Divider'
import Radio from '../../Radio'
import { FACTION_LIST } from '../../../constants/nft'
import ActionButton from '../../Button/ActionButton'
import { FactionsCard } from './FactionCard'
import { useI18n } from 'react-simple-i18n'
import { useNameValid } from '../../../hooks/useNFT'
import { useState } from 'react'
import Input from '../../Input'

export default function MintModal({
  isOpen,
  name,
  onSetName,
  sex,
  onSetSex,
  faction,
  onSetFaction,
  onNext,
  customOnDismiss
}: {
  isOpen: boolean
  name: string
  onSetName: (name: string) => void
  sex: number
  onSetSex: (sex: number) => void
  faction: number
  onSetFaction: (faction: number) => void
  onNext: () => void
  customOnDismiss: () => void
}) {
  const { t } = useI18n()
  const [curName, setCurName] = useState('')
  const { loading, valid } = useNameValid(curName)

  return (
    <Modal
      customIsOpen={isOpen}
      customOnDismiss={customOnDismiss}
      closeIcon
      width={'460px'}
      maxWidth={'460px'}
      title={t('profile.mintNFT')}
    >
      <Stack sx={{ ' p': { color: '#141420', fontSize: 16, padding: '0 24px' } }}>
        <Divider color={'#F1F0F2'} />
        <Stack spacing={24}>
          <Stack mt={32} direction={'row'} alignItems={'center'} paddingRight={24}>
            <Typography>NFT {t('profile.name')}</Typography>
            <Input
              type={'text'}
              maxLength={15}
              endAdornment={'.Lynk'}
              placeholder={t('enterName')}
              value={name ?? ''}
              onChange={e => {
                setCurName(e.target.value)
                onSetName(e.target.value)
              }}
              style={{ flex: 1, height: 50, padding: '0 25px', borderRadius: '25px', border: '1px solid #C5C4D6' }}
            />
          </Stack>
          <Stack direction={'row'} alignItems={'center'}>
            <Typography>{t('profile.selectSex')}</Typography>
            <RadioGroup sx={{ display: 'flex', flexDirection: 'row' }}>
              <Radio onChange={() => onSetSex(1)} checked={sex === 1} label={t('boy')} />
              <Radio onChange={() => onSetSex(0)} checked={sex === 0} label={t('girl')} />
            </RadioGroup>
          </Stack>
          <Stack direction={'row'}>
            <Typography sx={{ wordBreak: 'keep-all' }}>{t('profile.selectFactions')}</Typography>
            <Grid container sx={{ justifyContent: 'space-between', paddingRight: 24 }}>
              {FACTION_LIST.map(({ value, name, color }) => {
                return (
                  <FactionsCard
                    key={value}
                    onClick={() => onSetFaction(value)}
                    active={faction === value}
                    factions={name}
                    color={color}
                  />
                )
              })}
            </Grid>
          </Stack>
        </Stack>
        <Stack sx={{ margin: 24, marginTop: 32 }}>
          <ActionButton
            pending={loading}
            pendingText={'loading'}
            disableAction={name.length === 0 || !valid}
            onAction={onNext}
            actionText={name.length === 0 ? t('enterName') : !valid ? t('fledgling.nameUsed') : t('mint')}
            style={{ height: 50, width: '100%', borderRadius: 30, fontSize: 16, background: 'black' }}
          />
        </Stack>
      </Stack>
    </Modal>
  )
}
