import { Box, Stack, styled } from '@mui/material'
import { routes } from '../../constants/routes'
import { Tab } from '../../components/Header'
import { NavLink, useLocation } from 'react-router-dom'
import React, { useState, useEffect, useRef } from 'react'
import Divider from '../../components/Divider'
import SubHeader from 'components/Header/SubHeder'
import { useInviter } from '../../state/application/hooks'
import config from 'config'
import moment from 'moment'

const StyledNavLink = styled(NavLink)(({ theme }) => ({
  fontSize: 16,
  color: theme.textColor.text5,
  textDecoration: 'none',
  padding: '20px 0',
  '&: hover': {
    borderBottom: '2px solid #141420'
  },
  '&.active': {
    color: 'black',
    borderBottom: '2px solid #141420'
  }
}))
const StyledWrapper = styled(Box)(({}) => ({
  width: '100%',
  maxWidth: 1420,
  margin: 'auto',
  padding: '0 20px'
}))

export const Tabs: Tab[] = [
  { title: 'My NFT', route: routes.profile.nfts, disable: false },
  { title: 'My Stake', route: routes.profile.staking, disable: true },
  { title: 'My Invitation', route: routes.profile.invite, disable: false },
  { title: 'My History', route: routes.profile.history, disable: false }
]

export default function Profile({ children }: { children: React.ReactNode }) {
  const { pathname } = useLocation()
  const timer = useRef<any>()
  const { allOpenTime } = config
  const inviter = useInviter()
  const [tabs, setTabs] = useState(Tabs)
  const enableTabs = Tabs.map(item => {
    return { ...item, disable: false }
  })

  useEffect(() => {
    timer.current = setInterval(() => {
      if (moment(new Date()).valueOf() > allOpenTime) {
        setTabs(enableTabs)
        clearInterval(timer.current)
      } else {
        setTabs(Tabs)
      }
    }, 1000)
    return () => clearInterval(timer.current)
  }, [allOpenTime, enableTabs])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <Stack sx={{ width: '100%' }}>
      <SubHeader />
      <StyledWrapper
        sx={{
          display: { sm: 'grid', xs: 'flex' },
          justifyContent: { sm: 'unset', xs: 'space-between' },
          gridTemplateColumns: 'auto auto auto auto 1fr',
          gap: 20
        }}
      >
        {tabs.map(({ title, route, disable }) => {
          return (
            <StyledNavLink
              sx={{ fontSize: { xs: 12, sm: 16 }, pointerEvents: disable ? 'none' : 'auto' }}
              key={title}
              to={{ pathname: route ?? '/', search: inviter ? `?referrer=${window.btoa(inviter)}` : '' }}
              className={route ? (pathname.includes(route) ? 'active' : '') : ''}
            >
              {title}
            </StyledNavLink>
          )
        })}
      </StyledWrapper>
      <Divider />
      <StyledWrapper>{children}</StyledWrapper>
    </Stack>
  )
}
