import Modal from '../index'
import { MenuItem, Stack, Typography, useTheme } from '@mui/material'
import { useI18n } from 'react-simple-i18n'
import Select from '../../Select/Select'
import ActionButton from '../../Button/ActionButton'
import { SUPPORTED_TOKEN, TOKEN } from '../../../constants'
import { Token, TokenAmount } from '../../../constants/token'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { useActiveWeb3React } from '../../../hooks'
import { useState } from 'react'

export default function AssetModal({
  status,
  pending,
  isOpen,
  onAction,
  onBack,
  customOnDismiss,
  onSelectAsset,
  payAmount
}: {
  payAmount: TokenAmount | undefined
  status: string
  pending: boolean
  isOpen: boolean
  onAction: () => void
  onBack: () => void
  customOnDismiss: () => void
  onSelectAsset: (token: Token) => void
}) {
  const { t } = useI18n()
  const theme = useTheme()
  const { account } = useActiveWeb3React()
  const [token, setToken] = useState<TOKEN>(TOKEN.USDT)
  const balance = useTokenBalance(account ?? undefined, payAmount?.token)

  const enoughAsset = balance && payAmount && !balance.lessThan(payAmount)

  return (
    <Modal
      customOnDismiss={customOnDismiss}
      width={'420px'}
      maxWidth={'420px'}
      backIcon
      closeIcon
      onBack={onBack}
      customIsOpen={isOpen}
      title={t('mint')}
    >
      <Stack
        direction={'row'}
        sx={{
          height: 50,
          width: 360,
          margin: 'auto',
          border: '1px solid #C5C4D6',
          borderRadius: 25,
          overflow: 'hidden',
          marginTop: 20,
          background: '#FAFAFA'
        }}
      >
        <Typography
          sx={{ color: theme => theme.palette.primary.main }}
          color={'#141420'}
          fontWeight={'bold'}
          paddingLeft={25}
          lineHeight={'50px'}
          width={260}
        >
          {payAmount ? payAmount?.toFixed(2, { groupSeparator: ',' }) : ''}
        </Typography>
        <Stack width={2} height={'100%'} sx={{ backgroundColor: '#C5C4D6' }} />
        <Select
          style={{ border: 'none' }}
          width={100}
          height={50}
          value={token}
          onChange={e => {
            onSelectAsset(SUPPORTED_TOKEN[e.target.value as TOKEN])
            setToken(e.target.value as TOKEN)
          }}
        >
          <MenuItem value={TOKEN.USDT}>
            <Typography>USDT</Typography>
          </MenuItem>
        </Select>
      </Stack>
      <Stack
        direction={'row'}
        sx={{ width: 360, paddingTop: 10, margin: 'auto', justifyContent: 'flex-end', ' p': { fontSize: 12 } }}
      >
        <Typography color={'#8A899E'}>{t('availableBalance')}:&nbsp;</Typography>
        <Typography>{balance ? balance.toFixed(2, { groupSeparator: ',' }) : '--'}</Typography>
        <Typography color={'#8A899E'}>&nbsp;USDT</Typography>
      </Stack>
      <Stack sx={{ margin: '20px auto', width: 360 }}>
        <ActionButton
          pending={pending}
          pendingText={t('approving')}
          disableAction={!enoughAsset}
          style={{
            width: 360,
            background: theme.palette.primary.main,
            height: theme.height.medium
          }}
          onAction={onAction}
          actionText={!enoughAsset ? t('insufficient') : status}
        />
      </Stack>
    </Modal>
  )
}
