import useModal from '../../hooks/useModal'
import { registerToken } from '../../utils'
// import { useInviter } from '../../state/application/hooks'
import config from 'config'
import moment from 'moment'

const StyledNavLink = styled(NavLink)({})

const StyledBetween = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between'
})

const Text1 = styled(Typography)(({ theme }) => ({
  color: theme.palette.text.primary
}))

const Text2 = styled(Text1)(({}) => ({
  color: '#000'
}))

const navLinkSx = {
  cursor: 'pointer',
  textDecoration: 'none',
  fontSize: 20,
  fontWeight: 500,
  color: '#000',
  // color: (theme: Theme) => theme.palette.text.secondary,
  padding: '13px 0px',
  width: '100%',
  textAlign: 'left',
  display: 'flex',
  justifyContent: 'flex-start',
  alignItems: 'center',
  '& img': {
    marginRight: '10px',
    height: 24
  },
  '&.active': {
    // color: (theme: Theme) => theme.palette.primary.main
    color: (theme: Theme) => theme.palette.text.secondary
  },
  '&:hover': {
    color: (theme: Theme) => theme.palette.text.primary
  }
} as const

export default function MobileMenu({ isOpen, onDismiss }: { isOpen: boolean; onDismiss: () => void }) {
  const Tabs = useTabs()
  const { t } = useI18n()
  // const { pathname } = useLocation()
  const { showModal } = useModal()
  // const inviter = useInviter()
  const { allOpenTime } = config
  const { account } = useActiveWeb3React()
  const apBalance = useTokenBalance(account ?? undefined, AP)
  const lynkBalance = useTokenBalance(account ?? undefined, LRT)
  const usdtBalance = useTokenBalance(account ?? undefined, USDT)
  const alyxBalance = useTokenBalance(account ?? undefined, LYNK)
  const [openSwapModal, setOpenSwapModal] = useState(false)
  const swapTimer = useRef<any>()
  const [showSwap, setShowSwap] = useState(moment(new Date()).valueOf() > allOpenTime)

  useEffect(() => {
    swapTimer.current = setInterval(() => {
      if (moment(new Date()).valueOf() > allOpenTime) {
        setShowSwap(true)
        clearInterval(swapTimer.current)
      } else {
        setShowSwap(false)
      }
    }, 1000)
    return () => clearInterval(swapTimer.current)
  }, [allOpenTime])

  return (
    <Drawer
      open={isOpen}
      onClose={onDismiss}
      anchor="left"
      BackdropProps={{ sx: { backgroundColor: 'rgba(0,0,0,0.6)' } }}
      PaperProps={{
        sx: {
          top: 0,
          width: 250,
          overflow: 'hidden',
          padding: '20px 20px 200px',
          paddingTop: theme => ({ xs: theme.height.mobileHeader, sm: theme.height.header })
          // top: theme => ({ xs: theme.height.mobileHeader, sm: theme.height.header })
        }
      }}
      sx={{
        zIndex: theme => theme.zIndex.drawer,
        overflow: 'hidden',
        top: 0
        // top: theme => ({ xs: theme.height.mobileHeader, sm: theme.height.header })
      }}
    >
      <SwapModal isOpen={openSwapModal} onDismiss={() => setOpenSwapModal(false)} />
      <Box>
        <Box mb={20}>
          <img src={logo} width="140px" />
          <StyledBetween mt={15}>
            <Menu
              sx={{
                height: 24
              }}
            />
          </StyledBetween>
        </Box>
        <Box
          display="grid"
          gap="15px"
          sx={{
            height: 'calc(60vh - 120px)',
            overflow: 'auto',
            '& a': {
              padding: '13px 0'
            }
          }}
        >
          {Tabs.map(({ title, route, link, titleContent, subTab, icon, disable }) => {
            const content = titleContent ?? title
            return subTab ? (
              <Accordion icon={icon} disable={!!disable} placeholder={title} key={title}>
                {subTab.map(sub => {
                  const subContent = sub.titleContent ?? sub.title
                  return sub.comingSoon ? (
                    <MenuItem key={sub.title}>
                      <Typography onClick={() => showModal(<TipModal tip={t('coming')} />)}>
                        {sub.titleContent ?? sub.title}
                      </Typography>
                    </MenuItem>
                  ) : sub.link ? (
                    <MenuItem key={sub.link}>
                      <ExternalLink href={sub.link} sx={navLinkSx}>
                        {sub?.icon && <img src={sub.icon} />}
                        {subContent}
                      </ExternalLink>
                    </MenuItem>
                  ) : (
                    <MenuItem key={sub.title} onClick={onDismiss}>
                      <StyledNavLink to={sub.route ?? ''} className={'link'} sx={navLinkSx}>
                        {sub?.icon && <img src={sub.icon} />}
                        {subContent}
                      </StyledNavLink>
                    </MenuItem>
                  )
                })}
              </Accordion>
            ) : link ? (
              <ExternalLink href={link} sx={navLinkSx} key={link}>
                {icon && <img src={icon} />}
                {content}
              </ExternalLink>
            ) : (
              route && (
                <StyledNavLink
                  style={{ pointerEvents: disable ? 'none' : 'auto' }}
                  key={title}
                  id={`${route}-nav-link`}
                  to={route}
                  sx={navLinkSx}
                  onClick={onDismiss}
                >
                  {icon && <img src={icon} />}
                  {content}
                </StyledNavLink>
              )
            )
          })}
        </Box>

        <Box
          sx={{
            position: 'absolute',
            bottom: 20,
            left: 20,
            right: 20
          }}
        >
          {showSwap ? (
            <Button onClick={() => setOpenSwapModal(true)} style={{ height: 20, fontSize: 12, marginBottom: 10 }}>
              {t('swapALYX')}
            </Button>
          ) : null}
          <Stack spacing={10}>
            <StyledBetween>
              <Box display={'flex'}>
                <Text1>AP&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</Text1>
                <Text2>{apBalance ? apBalance.toFixed(2, { groupSeparator: ',' }) : '--'}</Text2>
              </Box>
              <Typography
                fontSize={12}
                sx={{ textDecoration: 'underline', cursor: 'pointer' }}
                onClick={() => registerToken(AP.address, AP.symbol ?? '', AP.decimals, '')}
              >
                Add token
              </Typography>
            </StyledBetween>
            <StyledBetween>
              <Box display={'flex'}>
                <Text1>LRT&nbsp;&nbsp;&nbsp;:</Text1>
                <Text2>{lynkBalance ? lynkBalance.toFixed(2, { groupSeparator: ',' }) : '--'}</Text2>
              </Box>
              <Typography
                fontSize={12}
                sx={{ textDecoration: 'underline', cursor: 'pointer' }}
                onClick={() => registerToken(LRT.address, LRT.symbol ?? '', LRT.decimals, '')}
              >
                Add token
              </Typography>
            </StyledBetween>
            <StyledBetween>
              <Box display={'flex'}>
                <Text1>LYNK&nbsp;:</Text1>
                <Text2>{account && alyxBalance ? alyxBalance?.toFixed(2, { groupSeparator: ',' }) : '--'}</Text2>
              </Box>
              <Typography
                fontSize={12}
                sx={{ textDecoration: 'underline', cursor: 'pointer' }}
                onClick={() => registerToken(LYNK.address, LYNK.symbol ?? '', LYNK.decimals, '')}
              >
                Add token
              </Typography>
            </StyledBetween>
            <StyledBetween>
              <Box display={'flex'}>
                <Text1>USDT&nbsp;:</Text1>
                <Text2>{usdtBalance ? usdtBalance.toFixed(2, { groupSeparator: ',' }) : '--'}</Text2>
              </Box>
              <Typography
                fontSize={12}
                sx={{ textDecoration: 'underline', cursor: 'pointer' }}
                onClick={() => registerToken(USDT.address, USDT.symbol ?? '', USDT.decimals, '')}
              >
                Add token
              </Typography>
            </StyledBetween>
          </Stack>

          <Box
            mt={20}
            sx={{
              display: 'flex',
              justifyContent: 'space-evenly',
              '& img': {
                height: 24
              }
            }}
          >
            <Social />
          </Box>
        </Box>
      </Box>
    </Drawer>
  )
}

function Accordion({
  children,
  placeholder,
  icon,
  disable
}: {
  children: React.ReactNode
  placeholder: string
  icon?: string
  disable: boolean
}) {
  const [isOpen, setIsOpen] = useState(false)
  const handleClick = useCallback(() => {
    if (disable) return
    setIsOpen(state => !state)
  }, [])
  return (
    <>
      <Box sx={navLinkSx} display="flex" alignItems="center" onClick={handleClick}>
        {icon && <img src={icon} />}
        {placeholder}{' '}
        <ExpandMore
          sx={{
            transform: isOpen ? 'rotate(180deg)' : ''
          }}
        />
      </Box>

      {isOpen && (
        <Box ml={18} mt={-25} mb={12}>
          {children}
        </Box>
      )}
    </>
  )
}

export function SelectLanguage() {
  const { i18n } = useI18n()
  const list = [
    { label: 'English', value: 'en' }
    // { label: '简体中文', value: 'cn' }
  ]
  return (
    <Select
      style={{
        border: '1px solid #ccc !important',
        borderRadius: 20,
        fontSize: 12
      }}
      width={120}
      height={40}
      value={i18n.getLang()}
      placeholder=""
      onChange={e => i18n.setLang(e.target.value)}
    >
      {list.map(item => (
        <MenuItem value={item.value} key={item.value} selected={item.value === i18n.getLang()}>
          {item.label}
        </MenuItem>
      ))}
    </Select>
  )
}

export function Social() {
  return (
    <>
      <ExternalLink href={'https://twitter.com/lynksanctuary '}>
        <img src={twitter} />
      </ExternalLink>
      <ExternalLink href={'https://discord.gg/6JcgvUzdPj '}>
        <img src={discord} />
      </ExternalLink>
      <ExternalLink href={'https://medium.com/@lynksanctuary'}>
        <img src={mid} />
      </ExternalLink>
      <ExternalLink href={'https://lynksanctuary.gitbook.io'}>
        <img src={gitbook} />
      </ExternalLink>
    </>
  )
}
