import { PageModel } from '../../constants'
import { Box, Stack } from '@mui/material'
import ClaimModal from '../../components/Modal/base/ClaimModal'

import { NFTCard } from '../../components/Card'
import { useI18n } from 'react-simple-i18n'
import { NFT } from '../../utils/fetch/graph'
import useBreakpoint from '../../hooks/useBreakpoint'
import { useHistory } from 'react-router-dom'
import useModal from '../../hooks/useModal'
import { useCallback } from 'react'
import TransactionPendingModal from '../../components/Modal/TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../../components/Modal/TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../../components/Modal/TransactionModals/MessageBox'
import { useStaking } from '../../hooks/useStaking'
import { useUserHasSubmitted } from '../../state/transactions/hooks'
import { useActiveWeb3React } from '../../hooks'
import ActionButton from '../../components/Button/ActionButton'
import { useInviter } from '../../state/application/hooks'

export function StakingCard({ nft, shopTip }: { nft: NFT; shopTip: boolean }) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const isSmDown = useBreakpoint('sm')
  const { push } = useHistory()
  const { showModal, hideModal } = useModal()
  const { withdraw } = useStaking()
  const inviter = useInviter()
  const { submitted: withdrawing } = useUserHasSubmitted(`${account}_withdraw_${nft.id}`)

  const withdrawCallback = useCallback(
    async (tokenId: string) => {
      showModal(<TransactionPendingModal />)
      withdraw(tokenId)
        .then(() => {
          hideModal()
          showModal(<TransactionSubmittedModal />)
        })
        .catch((err: any) => {
          hideModal()
          showModal(
            <MessageBox type="error">
              {err.error && err.error.message
                ? err.error.message
                : err?.data?.message
                ? err?.data?.message
                : err.message ?? 'Network Error'}
            </MessageBox>
          )
          console.error(err)
        })
    },
    [showModal, withdraw, hideModal]
  )

  return (
    <NFTCard
      showTip={shopTip}
      item={nft}
      onClick={() =>
        push({
          pathname: `/bird/${PageModel.DEFAULT}/${nft.id}`,
          search: inviter ? `?referrer=${window.btoa(inviter)}` : ''
        })
      }
      marketMobile={isSmDown}
      small={isSmDown}
    >
      <Box>
        {/*<Stack padding={'10px 15px'} direction={'row'} spacing={6} alignItems={'center'}>*/}
        {/*  <Typography fontWeight={'bold'}>*/}
        {/*    Pre-price:*/}
        {/*    {Number(*/}
        {/*      nft.charisma + Number(nft.vitality) * 0.05 + Number(nft.intellect) * 0.05 + Number(nft.dexterity) * 0.05*/}
        {/*    )*/}
        {/*      .toFixed(4)*/}
        {/*      .replace(/0+$/g, '')}*/}
        {/*  </Typography>*/}
        {/*  <Image height={'16px'} width={'16px'} src={USDT_IMG} />*/}
        {/*</Stack>*/}
        <Stack padding={'0 15px'} spacing={10} direction={'row'}>
          <Box width={'100%'} mt={10} mb={10} gap={isSmDown ? 5 : 20} display="grid" gridTemplateColumns={'1fr 1fr'}>
            <ActionButton
              style={{ width: '100%', background: 'black', height: '100%', fontSize: 12 }}
              pending={withdrawing}
              pendingText={t('withdrawing')}
              onAction={() => {
                withdrawCallback(nft.id)
              }}
              actionText={t('withdraw')}
            />
            <ActionButton
              style={{ width: '100%', background: 'black' }}
              actionText={`${t('claim')}`}
              onAction={() => {
                showModal(<ClaimModal tokenId={nft.id} />)
              }}
              width={'100%'}
            />
          </Box>
        </Stack>
      </Box>
    </NFTCard>
  )
}
