import Modal from '../index'
import { useI18n } from 'react-simple-i18n'
import { Box, Grid, Stack, Typography } from '@mui/material'
import { useCallback } from 'react'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import useModal from '../../../hooks/useModal'
import { usePackage } from '../../../hooks/usePackage'
import Image from '../../Image'
import { CurrencyAmount, TokenAmount } from '../../../constants/token'
import { useTokenBalance } from '../../../state/wallet/hooks'
import { useActiveWeb3React } from '../../../hooks'
import JSBI from 'jsbi'
import useBreakpoint from '../../../hooks/useBreakpoint'
import { LYNK, AP } from '../../../constants'
import { ApprovalState, useApproveCallback } from '../../../hooks/useApproveCallback'
import ActionButton from '../../Button/ActionButton'

export default function BuyPropsModal({ isOpen, onDismiss }: { isOpen: boolean; onDismiss: () => void }) {
  const { t } = useI18n()
  const { showModal, hideModal } = useModal()
  const { mint, packages } = usePackage()
  const { account } = useActiveWeb3React()
  const alyxBalance = useTokenBalance(account ?? undefined, LYNK)
  const balance = account ? alyxBalance : undefined
  const isDownSm = useBreakpoint('sm')
  const payAmount = new TokenAmount(LYNK, JSBI.BigInt(1))
  const [approvalState, approveCallback] = useApproveCallback(payAmount, AP.address)
  const mintCallback = useCallback(
    async (index: string, amount: string) => {
      showModal(<TransactionPendingModal />)
      mint(index, amount)
        .then(() => {
          hideModal()
          showModal(<TransactionSubmittedModal />)
        })
        .catch((err: any) => {
          hideModal()
          showModal(
            <MessageBox type="error">
              {err.error && err.error.message
                ? err.error.message
                : err?.data?.message
                ? err?.data?.message
                : err.message ?? 'Network Error'}
            </MessageBox>
          )
          console.error(err)
        })
    },
    [showModal, mint, hideModal]
  )

  return (
    <Modal
      customOnDismiss={onDismiss}
      customIsOpen={isOpen}
      width={'640px'}
      maxWidth={'640px'}
      title={`${t('buyProps')}`}
      closeIcon
    >
      <Typography marginTop={20} marginLeft={20}>
        {t('balance')}: {alyxBalance ? alyxBalance?.toFixed(2, { groupSeparator: ',' }) : '--'}
        {` LYNK`}
      </Typography>
      <Grid container direction={'row'} mt={10} mb={20} pl={20} pr={20}>
        {packages.map((item, index) => {
          return (
            <Grid xs={isDownSm ? 6 : 3} item key={index} spacing={'6px'} sx={{ padding: 12 }}>
              <Stack alignItems={'center'} spacing={10}>
                <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
                  <Image style={{ margin: 'auto' }} width={100} height={100} src={`/images/gift${index + 1}.png`} />
                </Box>
                <Stack direction={'row'} alignItems={'center'} sx={{ fontSize: 12, ' p': { fontSize: 12 } }}>
                  <Typography color={'#8A899E'}>Amount：</Typography>
                  {item.result?.[0]?.[2]
                    ? CurrencyAmount.ether(item.result?.[0]?.[2].toString()).toFixed(2, { groupSeparator: ',' })
                    : '--'}{' '}
                  AP
                </Stack>
                <Stack direction={'row'} alignItems={'center'} sx={{ fontSize: 12, ' p': { fontSize: 12 } }}>
                  <Typography color={'#8A899E'}>Price：</Typography>
                  {item.result?.[0]?.[1]
                    ? CurrencyAmount.ether(item.result?.[0]?.[1].toString()).toFixed(2, { groupSeparator: ',' })
                    : '--'}{' '}
                  LYNK
                </Stack>
                <ActionButton
                  pending={approvalState === ApprovalState.PENDING}
                  pendingText={'Pending'}
                  actionText={
                    approvalState === ApprovalState.NOT_APPROVED
                      ? 'Approve'
                      : balance &&
                        item.result?.[0]?.[1] &&
                        !JSBI.lessThan(JSBI.BigInt(item.result?.[0]?.[1]), JSBI.BigInt(balance?.raw?.toString()))
                      ? t('shortInsufficient') + ' LYNK'
                      : t('buy')
                  }
                  disableAction={
                    !item.result?.[0]?.[1] ||
                    !balance ||
                    JSBI.greaterThan(JSBI.BigInt(item.result?.[0]?.[1]), JSBI.BigInt(balance?.raw?.toString()))
                  }
                  onAction={() =>
                    approvalState === ApprovalState.NOT_APPROVED
                      ? approveCallback()
                      : mintCallback(index.toString(), item.result?.[0]?.[1])
                  }
                  style={{ width: '100%', fontSize: 12, background: 'black', height: 36 }}
                />
              </Stack>
            </Grid>
          )
        })}
      </Grid>
    </Modal>
  )
}
