import ReportGmailerrorredOutlinedIcon from '@mui/icons-material/ReportGmailerrorredOutlined'
// import CheckCircleOutlineOutlinedIcon from '@mui/icons-material/CheckCircleOutlineOutlined'
import { Typography, Box } from '@mui/material'
import { useActiveWeb3React } from 'hooks/'
import { ExternalLink } from 'theme/components'
import { getEtherscanLink } from 'utils'
import SuccessIcon from 'assets/images/success_icon.png'
import { useI18n } from 'react-simple-i18n'
import useBreakpoint from '../../../hooks/useBreakpoint'
import CONFIG from 'config'

export default function TransactionPopup({
  hash,
  success,
  summary
}: {
  hash?: string
  success?: boolean
  summary?: string
}) {
  const { chainId } = useActiveWeb3React()
  const { t } = useI18n()
  const isSmDown = useBreakpoint('sm')

  return (
    <Box fontSize={0}>
      <Box display="flex" alignItems="flex-start" flexWrap="nowrap">
        <div style={{ paddingRight: 16 }}>
          {success ? (
            <img src={SuccessIcon} height={isSmDown ? 30 : 50} width={isSmDown ? 30 : 50} />
          ) : (
            <ReportGmailerrorredOutlinedIcon color="error" height={isSmDown ? 30 : 50} width={isSmDown ? 30 : 50} />
          )}
        </div>
        <Box>
          <Typography
            variant="inherit"
            sx={{
              wordBreak: 'break-all',
              fontSize: { xs: 12, sm: 16 }
            }}
            maxWidth={140}
            fontWeight="bold"
            mt={3}
            mb={10}
          >
            {summary ? t(summary) : hash ? 'Hash: ' + hash.slice(0, 8) + '...' + hash.slice(58, 65) : '-'}
          </Typography>{' '}
          {chainId && hash && (
            <ExternalLink
              underline="hover"
              href={getEtherscanLink(chainId ? chainId : 1, hash, 'transaction')}
              style={{ fontSize: 12 }}
            >
              {t(`viewScan.${CONFIG.chainName}`)}
            </ExternalLink>
          )}
        </Box>
      </Box>
    </Box>
  )
}
