import { useTheme, Box, Typography } from '@mui/material'
// import Spinner from 'components/Spinner'
import PendingIcon from 'assets/images/pending_icon.png'
import useBreakpoint from 'hooks/useBreakpoint'
import Modal from '../index'
import { useI18n } from 'react-simple-i18n'

export default function TransactionPendingModal({ pendingText, title }: { pendingText?: string; title?: string }) {
  const theme = useTheme()
  const { t } = useI18n()
  const isSmDown = useBreakpoint('sm')
  return (
    <Modal closeIcon title={title}>
      <Box display="grid" padding="40px 24px" gap="24px" justifyItems="center">
        {/* <Spinner size="40px" /> */}
        <img src={PendingIcon} width={isSmDown ? 40 : 80} />
        <Typography fontWeight={400} fontSize={16}>
          {/* Waiting For Confirmation */}
          {t('confirming')}
        </Typography>
        <Typography fontWeight={400} fontSize={14} textAlign="center" color={theme.textColor.text3}>
          {/* {pendingText || 'Please initiate transaction in your wallet'} */}
          {pendingText || t('confirmInWallet')}
        </Typography>
      </Box>
    </Modal>
  )
}
