import React, { useMemo } from 'react'
import Logo from './LogoBase'
import { Currency } from '../../../constants/token/currency'
import { Token } from '../../../constants/token/token'

export const getTokenLogoURL = (address: string) => `/images/icons/${address}.png`

export default function CurrencyLogo({
  currency,
  size = '24px',
  style
}: {
  currency?: Currency
  size?: string
  style?: React.CSSProperties
}) {
  const srcs: string[] = useMemo(() => {
    if (currency instanceof Token) {
      return [getTokenLogoURL(currency.symbol ?? '')]
    }
    return [getTokenLogoURL(currency?.symbol ?? '')]
  }, [currency])
  console.log('srcs', srcs)
  return (
    <Logo
      style={{
        ...style,
        width: size,
        height: size,
        borderRadius: size,
        boxShadow: ' 0px 6px 10px rgba(0, 0, 0, 0.075)'
      }}
      srcs={srcs}
      alt={`${currency?.symbol ?? 'token'} logo`}
    />
  )
}

export const getAssetLogoURL = (address: string) => `/images/icons/${address}.png`

export function AssetLogo({
  address,
  size = '16px',
  style
}: {
  address: string
  size?: string
  style?: React.CSSProperties
}) {
  const srcs: string[] = useMemo(() => {
    return [getAssetLogoURL(address)]
  }, [address])
  return (
    <Logo
      style={{
        ...style,
        width: size,
        height: size,
        borderRadius: size,
        boxShadow: ' 0px 6px 10px rgba(0, 0, 0, 0.075)'
      }}
      srcs={srcs}
      alt={``}
    />
  )
}
