import Modal from '../index'
import { useI18n } from 'react-simple-i18n'
import { Box, Stack, Typography } from '@mui/material'
import { useClaim, useReward } from '../../../hooks/useStaking'
import ActionButton from '../../Button/ActionButton'
import { useCallback } from 'react'
import TransactionPendingModal from '../TransactionModals/TransactionPendingModal'
import TransactionSubmittedModal from '../TransactionModals/TransactiontionSubmittedModal'
import MessageBox from '../TransactionModals/MessageBox'
import useModal from '../../../hooks/useModal'
import { useUserHasSubmitted } from '../../../state/transactions/hooks'
import { useActiveWeb3React } from '../../../hooks'

export default function ClaimModal({ tokenId }: { tokenId: string }) {
  const { t } = useI18n()
  const { account } = useActiveWeb3React()
  const { reward } = useReward(tokenId)
  const { showModal, hideModal } = useModal()
  const { claim } = useClaim()

  const { submitted: claiming } = useUserHasSubmitted(`${account}_claim_${tokenId}`)

  const claimCallback = useCallback(async () => {
    showModal(<TransactionPendingModal />)
    claim(tokenId)
      .then(() => {
        hideModal()
        showModal(<TransactionSubmittedModal />)
      })
      .catch((err: any) => {
        hideModal()
        showModal(
          <MessageBox type="error">
            {err.error && err.error.message
              ? err.error.message
              : err?.data?.message
              ? err?.data?.message
              : err.message ?? 'Network Error'}
          </MessageBox>
        )
        console.error(err)
      })
  }, [showModal, claim, tokenId, hideModal])

  return (
    <Modal title={`${t('claim')} AP`}>
      {!reward || reward.equalTo('0') ? (
        <Typography margin={80} textAlign={'center'} fontWeight={'bold'} fontSize={18}>
          {t('noReward')}
        </Typography>
      ) : (
        <>
          <Typography color={'#141420'} margin={30} textAlign={'center'}>
            {t('validClaim')}
          </Typography>
          <Stack direction={'row'} justifyContent={'center'}>
            <Typography lineHeight={0} fontSize={46} fontWeight={'bold'} display={'flex'}>
              {reward ? reward.toFixed(2, { groupSeparator: ',' }).toString() : '--'}
            </Typography>
            <Typography alignSelf={'end'} color={'#141420'}>
              AP
            </Typography>
          </Stack>
          <Box margin={30}>
            <ActionButton
              pending={claiming}
              style={{ height: 50, width: '100%' }}
              onAction={claimCallback}
              actionText={claiming ? t('claiming') : t('continue')}
            />
          </Box>
        </>
      )}
    </Modal>
  )
}
