const postQuery = async (endpoint: string, query: string) => {
  const options = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ query })
  }
  const response = await fetch(endpoint, options)
  const data = await response.json()
  if (data.errors) {
    throw new Error(data.errors[0].message)
  } else {
    return data
  }
}

export async function getOrderList(
  fraction: number[],
  sex: number[],
  princeRange: (CurrencyAmount | undefined)[],
  level: number[],
  order: string,
  size = 10,
  page = 1
): Promise<Order[]> {
  const minPrice = princeRange[0] ? `priceInAcceptToken_gte: "${princeRange[0].raw.toString()}"` : ''
  const maxPrice = princeRange[1] ? `priceInAcceptToken_lte: "${princeRange[1].raw.toString()}"` : ''
  const level_in = level && level.length > 0 ? `level_in: [${level}]` : ''
  const sex_in = sex && sex.length > 0 ? `sex_in: [0,${sex}]` : 'sex_in: [0]'
  const factions_in = fraction && fraction.length > 0 ? `factions_in: [${fraction}]` : ''
  const orderStr = order && order != '' ? `orderBy: priceInAcceptToken,orderDirection: ${order}` : ''
  const query = `
  {
    marketGoodsEntities(
        first: ${size}, skip: ${size * (page - 1)}
        ${orderStr}
        where: {
           ${minPrice}
           ${maxPrice}
           ${level_in}
           ${sex_in}
           ${factions_in}
           onSale:true
        }
    ) {
        id
        level
        priceInAcceptToken
        charisma
        charismaThreshold
        vitality
        vitalityThreshold
        intellect
        intellectThreshold
        dexterity
        dexterityThreshold
        name
        sex
        grade
        factions
    }
}
  `

  try {
    const response = await postQuery(SUBGRAPH, query)
    return response.data.marketGoodsEntities
  } catch (error) {
    return []
  }
}

export async function getAccountNFT(account: string): Promise<NFT[]> {
  if (!isAddress(account)) return []
  const query = `{ lynknftentities(where: {owner: "${account}"}, orderBy: createdTime, orderDirection: desc) {  id  createdTime   owner   isList   name    listIndex  level   isStaking  charisma   charismaThreshold   vitality    vitalityThreshold    intellect    intellectThreshold    dexterity    dexterityThreshold  }  userEntities(subgraphError: deny) {    id   level  }}`

  try {
    const response = await postQuery(SUBGRAPH, query)
    return response.data.lynknftentities
  } catch (error) {
    return []
  }
}

